/**
 * Mobile accessibility enhancements
 */

import { mobileLayoutManager } from './mobileUtils';

/**
 * Mobile Accessibility Manager
 */
export class MobileAccessibilityManager {
  private static focusTracker: HTMLElement | null = null;
  private static announcer: HTMLElement | null = null;

  /**
   * Initialize mobile accessibility features
   */
  static initialize(): void {
    this.createScreenReaderAnnouncer();
    this.setupFocusManagement();
    this.enhanceTabNavigation();
    this.addTouchAccessibility();
    this.setupKeyboardNavigation();
  }

  /**
   * Create screen reader announcer
   */
  private static createScreenReaderAnnouncer(): void {
    if (this.announcer) return;

    this.announcer = document.createElement('div');
    this.announcer.setAttribute('aria-live', 'polite');
    this.announcer.setAttribute('aria-atomic', 'true');
    this.announcer.className = 'sr-only';
    this.announcer.style.cssText = `
      position: absolute !important;
      width: 1px !important;
      height: 1px !important;
      padding: 0 !important;
      margin: -1px !important;
      overflow: hidden !important;
      clip: rect(0, 0, 0, 0) !important;
      white-space: nowrap !important;
      border: 0 !important;
    `;

    document.body.appendChild(this.announcer);
  }

  /**
   * Announce message to screen readers
   */
  static announce(message: string, priority: 'polite' | 'assertive' = 'polite'): void {
    if (!this.announcer) this.createScreenReaderAnnouncer();

    this.announcer!.setAttribute('aria-live', priority);
    this.announcer!.textContent = message;

    // Clear after announcement
    setTimeout(() => {
      if (this.announcer) {
        this.announcer.textContent = '';
      }
    }, 1000);
  }

  /**
   * Setup focus management for mobile
   */
  private static setupFocusManagement(): void {
    // Track focus for mobile navigation
    document.addEventListener('focusin', (e) => {
      this.focusTracker = e.target as HTMLElement;
    });

    // Handle focus when switching tabs on mobile
    document.addEventListener('visibilitychange', () => {
      if (document.visibilityState === 'visible' && mobileLayoutManager.isMobile()) {
        this.restoreFocus();
      }
    });
  }

  /**
   * Restore focus to appropriate element
   */
  private static restoreFocus(): void {
    if (this.focusTracker && document.contains(this.focusTracker)) {
      this.focusTracker.focus();
    } else {
      // Focus first interactive element in current section
      const activeSection = document.querySelector('.main-content > :not(.hidden)');
      if (activeSection) {
        const firstInteractive = activeSection.querySelector('button, input, select, textarea, [tabindex]:not([tabindex="-1"])');
        if (firstInteractive) {
          (firstInteractive as HTMLElement).focus();
        }
      }
    }
  }

  /**
   * Enhance tab navigation for mobile
   */
  private static enhanceTabNavigation(): void {
    const tabContainer = document.querySelector('.mobile-tab-navigation');
    if (!tabContainer) return;

    // Add ARIA labels and roles
    tabContainer.setAttribute('role', 'tablist');
    tabContainer.setAttribute('aria-label', 'Main navigation');

    const tabs = tabContainer.querySelectorAll('.tab-nav-item');
    tabs.forEach((tab, index) => {
      tab.setAttribute('role', 'tab');
      tab.setAttribute('aria-selected', index === 0 ? 'true' : 'false');
      tab.setAttribute('tabindex', index === 0 ? '0' : '-1');
      
      const sectionId = tab.getAttribute('data-section');
      if (sectionId) {
        tab.setAttribute('aria-controls', sectionId);
        tab.id = `tab-${sectionId}`;
      }

      // Add keyboard navigation
      tab.addEventListener('keydown', (e) => {
        this.handleTabKeydown(e as KeyboardEvent, tabs, index);
      });

      // Announce tab changes
      tab.addEventListener('click', () => {
        const label = tab.querySelector('.tab-label')?.textContent || 'Section';
        this.announce(`Switched to ${label} section`);
        this.updateTabAria(tabs, index);
      });
    });
  }

  /**
   * Handle keyboard navigation in tabs
   */
  private static handleTabKeydown(e: KeyboardEvent, tabs: NodeListOf<Element>, currentIndex: number): void {
    let newIndex = currentIndex;

    switch (e.key) {
      case 'ArrowLeft':
        e.preventDefault();
        newIndex = currentIndex > 0 ? currentIndex - 1 : tabs.length - 1;
        break;
      case 'ArrowRight':
        e.preventDefault();
        newIndex = currentIndex < tabs.length - 1 ? currentIndex + 1 : 0;
        break;
      case 'Home':
        e.preventDefault();
        newIndex = 0;
        break;
      case 'End':
        e.preventDefault();
        newIndex = tabs.length - 1;
        break;
      case 'Enter':
      case ' ':
        e.preventDefault();
        (tabs[currentIndex] as HTMLElement).click();
        return;
    }

    if (newIndex !== currentIndex) {
      this.updateTabAria(tabs, newIndex);
      (tabs[newIndex] as HTMLElement).focus();
    }
  }

  /**
   * Update ARIA attributes for tabs
   */
  private static updateTabAria(tabs: NodeListOf<Element>, activeIndex: number): void {
    tabs.forEach((tab, index) => {
      tab.setAttribute('aria-selected', index === activeIndex ? 'true' : 'false');
      tab.setAttribute('tabindex', index === activeIndex ? '0' : '-1');
    });
  }

  /**
   * Add touch accessibility features
   */
  private static addTouchAccessibility(): void {
    // Add touch feedback for screen readers
    document.addEventListener('touchstart', (e) => {
      const target = e.target as HTMLElement;
      if (target.matches('button, .unit-card, .tab-nav-item')) {
        target.setAttribute('aria-pressed', 'true');
      }
    }, { passive: true });

    document.addEventListener('touchend', (e) => {
      const target = e.target as HTMLElement;
      if (target.matches('button, .unit-card, .tab-nav-item')) {
        target.removeAttribute('aria-pressed');
      }
    }, { passive: true });

    // Add double-tap to activate for complex elements
    let lastTap = 0;
    document.addEventListener('touchend', (e) => {
      const currentTime = new Date().getTime();
      const tapLength = currentTime - lastTap;
      
      if (tapLength < 500 && tapLength > 0) {
        const target = e.target as HTMLElement;
        if (target.matches('.unit-card, .army-composition')) {
          this.announce('Double tap to activate', 'assertive');
        }
      }
      lastTap = currentTime;
    });
  }

  /**
   * Setup keyboard navigation for mobile
   */
  private static setupKeyboardNavigation(): void {
    // Add skip links for mobile
    this.addSkipLinks();

    // Handle escape key to close modals/sheets
    document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape') {
        const activeModal = document.querySelector('.bottom-sheet.visible, .mobile-loading-overlay.visible');
        if (activeModal) {
          e.preventDefault();
          this.announce('Modal closed');
          // Trigger close event
          const closeBtn = activeModal.querySelector('.bottom-sheet-close');
          if (closeBtn) {
            (closeBtn as HTMLElement).click();
          }
        }
      }
    });
  }

  /**
   * Add skip links for mobile navigation
   */
  private static addSkipLinks(): void {
    const skipLinks = document.createElement('div');
    skipLinks.className = 'skip-links';
    skipLinks.innerHTML = `
      <a href="#main-content" class="skip-link">Skip to main content</a>
      <a href="#mobile-navigation" class="skip-link">Skip to navigation</a>
    `;

    document.body.insertBefore(skipLinks, document.body.firstChild);

    // Add main content ID if not present
    const mainContent = document.querySelector('.main-content');
    if (mainContent && !mainContent.id) {
      mainContent.id = 'main-content';
    }

    // Add navigation ID if not present
    const navigation = document.querySelector('.mobile-tab-navigation');
    if (navigation && !navigation.id) {
      navigation.id = 'mobile-navigation';
    }
  }

  /**
   * Enhance form accessibility for mobile
   */
  static enhanceFormAccessibility(): void {
    const forms = document.querySelectorAll('form');
    
    forms.forEach(form => {
      // Add form labels and descriptions
      const inputs = form.querySelectorAll('input, select, textarea');
      inputs.forEach(input => {
        const label = form.querySelector(`label[for="${input.id}"]`);
        if (!label && input.id) {
          // Create label if missing
          const newLabel = document.createElement('label');
          newLabel.setAttribute('for', input.id);
          newLabel.textContent = input.getAttribute('placeholder') || 'Input field';
          newLabel.className = 'sr-only';
          input.parentNode?.insertBefore(newLabel, input);
        }

        // Add required field announcements
        if (input.hasAttribute('required')) {
          input.setAttribute('aria-required', 'true');
          input.addEventListener('invalid', () => {
            this.announce('Required field is empty', 'assertive');
          });
        }
      });

      // Announce form submission
      form.addEventListener('submit', () => {
        this.announce('Form submitted');
      });
    });
  }

  /**
   * Add ARIA labels to dynamic content
   */
  static addDynamicLabels(): void {
    // Label unit cards
    const unitCards = document.querySelectorAll('.unit-card');
    unitCards.forEach(card => {
      const unitName = card.querySelector('.unit-name')?.textContent;
      if (unitName && !card.getAttribute('aria-label')) {
        card.setAttribute('aria-label', `Unit: ${unitName}`);
        card.setAttribute('role', 'button');
      }
    });

    // Label army compositions
    const compositions = document.querySelectorAll('.army-composition');
    compositions.forEach((comp, index) => {
      if (!comp.getAttribute('aria-label')) {
        comp.setAttribute('aria-label', `Army composition ${index + 1}`);
        comp.setAttribute('role', 'article');
      }
    });

    // Label combat log entries
    const combatActions = document.querySelectorAll('.combat-action');
    combatActions.forEach((action, index) => {
      if (!action.getAttribute('aria-label')) {
        const actionText = action.textContent?.substring(0, 50) || 'Combat action';
        action.setAttribute('aria-label', `Combat action ${index + 1}: ${actionText}`);
      }
    });
  }

  /**
   * Cleanup accessibility features
   */
  static cleanup(): void {
    if (this.announcer && this.announcer.parentNode) {
      this.announcer.parentNode.removeChild(this.announcer);
      this.announcer = null;
    }

    const skipLinks = document.querySelector('.skip-links');
    if (skipLinks && skipLinks.parentNode) {
      skipLinks.parentNode.removeChild(skipLinks);
    }
  }
}

// Auto-initialize on mobile
if (mobileLayoutManager.isMobile()) {
  document.addEventListener('DOMContentLoaded', () => {
    MobileAccessibilityManager.initialize();
  });
}

// Cleanup when switching away from mobile
mobileLayoutManager.addLayoutChangeListener((mode) => {
  if (mode !== 'mobile') {
    MobileAccessibilityManager.cleanup();
  } else {
    MobileAccessibilityManager.initialize();
  }
});
