/**
 * Battle Simulation Service
 * Handles turn-based combat simulation with asymmetric mechanics
 * - Enemy attacks eliminate player groups completely
 * - Player attacks deal damage to enemies but never eliminate them
 * - Battle ends when all player groups are eliminated
 */

import {
  IBattleSimulationService,
  BattleConfiguration,
  BattleResult,
  BattleAnalysis,
  BattleState,
  CombatAction,
  BattleUtils,
  BATTLE_CONSTANTS
} from '../types/battleSimulation';
import { ArmyComposition, StackingGroup } from '../types/units';

export class BattleSimulationService implements IBattleSimulationService {
  // Cache for attack order calculations to avoid repeated sorting
  private attackOrderCache = new Map<string, StackingGroup[]>();
  private targetOrderCache = new Map<string, StackingGroup[]>();
  
  /**
   * Validate battle configuration before simulation
   */
  private validateBattleConfiguration(config: BattleConfiguration): void {
    if (!config) {
      throw new Error('Battle configuration is required');
    }

    if (!config.playerArmy) {
      throw new Error('Player army is required');
    }

    if (!config.playerArmy.stackingOrder || config.playerArmy.stackingOrder.length === 0) {
      throw new Error('Player army must have at least one unit group');
    }

    if (config.enemyGroupCount < 1) {
      throw new Error('Enemy group count must be at least 1');
    }

    if (config.enemyGroupCount > 100) {
      throw new Error('Enemy group count cannot exceed 100 (performance limit)');
    }

    // Validate each unit group
    for (const group of config.playerArmy.stackingOrder) {
      if (!group.unitName || group.unitName.trim() === '') {
        throw new Error('All unit groups must have a valid name');
      }

      if (group.count <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have a positive count`);
      }

      if (group.unitStrength <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have positive strength`);
      }

      if (group.totalHealth <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have positive health`);
      }
    }
  }

  /**
   * Simulate a single battle scenario with validation
   */
  simulateBattle(config: BattleConfiguration): BattleResult {
    // Validate configuration before starting
    this.validateBattleConfiguration(config);
    // Initialize battle state
    const state: BattleState = {
      currentTurn: 0,
      playerGroups: [...config.playerArmy.stackingOrder], // Copy to avoid mutation
      enemyGroupCount: config.enemyGroupCount,
      totalDamageDealt: 0,
      battleEnded: false,
      combatLog: []
    };

    // Main battle loop with additional safety checks
    let consecutiveNoActionTurns = 0;
    const maxNoActionTurns = 5;

    while (!this.shouldBattleEnd(state) && state.currentTurn < BATTLE_CONSTANTS.MAX_BATTLE_TURNS) {
      const previousLogLength = state.combatLog.length;
      state.currentTurn++;

      try {
        this.processTurn(state, config.playerGoesFirst);
      } catch (error) {
        throw new Error(`Battle processing failed on turn ${state.currentTurn}: ${error instanceof Error ? error.message : 'Unknown error'}`);
      }

      // Safety check: ensure progress is being made
      if (state.combatLog.length === previousLogLength) {
        consecutiveNoActionTurns++;
        if (consecutiveNoActionTurns >= maxNoActionTurns) {
          throw new Error(`Battle stalled: No actions taken for ${maxNoActionTurns} consecutive turns`);
        }
      } else {
        consecutiveNoActionTurns = 0;
      }

      // Additional safety check for infinite loops
      if (state.currentTurn > 1 && state.playerGroups.length === 0 && !state.battleEnded) {
        state.battleEnded = true;
        break;
      }
    }

    // Check if battle ended due to timeout
    if (state.currentTurn >= BATTLE_CONSTANTS.MAX_BATTLE_TURNS) {
      throw new Error(`Battle exceeded maximum duration of ${BATTLE_CONSTANTS.MAX_BATTLE_TURNS} turns`);
    }

    // Calculate battle statistics
    const statistics = BattleUtils.calculateBattleStatistics(state.combatLog, state.totalDamageDealt);

    // Create battle result
    return {
      outcome: 'player_eliminated',
      combatLog: state.combatLog,
      totalDamageDealtToEnemies: state.totalDamageDealt,
      battleDuration: state.currentTurn,
      playerSurvivalTurns: state.currentTurn,
      scenario: config.playerGoesFirst ? 'best_case' : 'worst_case',
      configuration: config,
      statistics
    };
  }

  /**
   * Simulate both best and worst case scenarios with validation
   */
  simulateBothScenarios(playerArmy: ArmyComposition, enemyGroupCount: number): BattleAnalysis {
    // Validate inputs
    if (!playerArmy) {
      throw new Error('Player army is required');
    }

    if (enemyGroupCount < 1 || enemyGroupCount > 100) {
      throw new Error('Enemy group count must be between 1 and 100');
    }
    // Best case: player goes first
    const bestCaseConfig: BattleConfiguration = {
      playerArmy,
      enemyGroupCount,
      playerGoesFirst: true
    };
    const bestCase = this.simulateBattle(bestCaseConfig);

    // Worst case: enemy goes first
    const worstCaseConfig: BattleConfiguration = {
      playerArmy,
      enemyGroupCount,
      playerGoesFirst: false
    };
    const worstCase = this.simulateBattle(worstCaseConfig);

    // Calculate comparison metrics
    const comparison = {
      damageDifference: bestCase.totalDamageDealtToEnemies - worstCase.totalDamageDealtToEnemies,
      survivalDifference: bestCase.playerSurvivalTurns - worstCase.playerSurvivalTurns,
      averageDamage: (bestCase.totalDamageDealtToEnemies + worstCase.totalDamageDealtToEnemies) / 2,
      averageSurvival: (bestCase.playerSurvivalTurns + worstCase.playerSurvivalTurns) / 2
    };

    return {
      bestCase,
      worstCase,
      comparison
    };
  }

  /**
   * Calculate the attack order for player groups (highest total strength first)
   * Uses caching to avoid repeated sorting of the same group composition
   */
  calculateAttackOrder(groups: StackingGroup[]): StackingGroup[] {
    // Create cache key based on group composition
    const cacheKey = groups.map(g => `${g.unitName}:${g.count}:${g.unitStrength}`).join('|');

    if (this.attackOrderCache.has(cacheKey)) {
      return this.attackOrderCache.get(cacheKey)!;
    }

    // Sort by total group strength (count √ó unitStrength), highest first
    const sorted = [...groups].sort((a, b) => {
      const totalStrengthA = a.count * a.unitStrength;
      const totalStrengthB = b.count * b.unitStrength;
      return totalStrengthB - totalStrengthA;
    });
    this.attackOrderCache.set(cacheKey, sorted);
    return sorted;
  }

  /**
   * Calculate enemy targeting order for player groups (highest health first)
   * Uses caching to avoid repeated sorting of the same group composition
   */
  calculateEnemyTargetOrder(groups: StackingGroup[]): StackingGroup[] {
    // Create cache key based on group composition
    const cacheKey = groups.map(g => `${g.unitName}:${g.count}:${g.totalHealth}`).join('|');

    if (this.targetOrderCache.has(cacheKey)) {
      return this.targetOrderCache.get(cacheKey)!;
    }

    const sorted = [...groups].sort((a, b) => b.totalHealth - a.totalHealth);
    this.targetOrderCache.set(cacheKey, sorted);
    return sorted;
  }

  /**
   * Determine if the battle should end (all player groups eliminated)
   */
  shouldBattleEnd(state: BattleState): boolean {
    return state.playerGroups.length === 0 || state.battleEnded;
  }

  /**
   * Process a single battle phase with proper overflow handling
   *
   * TERMINOLOGY:
   * - Unit's Turn: Each individual attack by a single unit
   * - Battle Phase: One complete cycle where every unit gets exactly one attack opportunity
   *
   * IMPORTANT: Each unit (player or enemy) can only take their turn ONCE per battle phase.
   * A battle phase consists of all units taking their turns, then the next phase begins.
   *
   * Example with 3 player groups and 2 enemy groups:
   * Battle Phase 1: Player1's turn ‚Üí Enemy1's turn ‚Üí Player2's turn ‚Üí Enemy2's turn ‚Üí Player3's turn (overflow)
   * Battle Phase 2: Player1's turn ‚Üí Enemy1's turn ‚Üí Player2's turn ‚Üí Enemy2's turn ‚Üí Player3's turn (if still alive)
   */
  processTurn(state: BattleState, playerGoesFirst: boolean): void {
    // Get the initial attack order at the start of the battle phase
    const initialPlayerAttackOrder = this.calculateAttackOrder(state.playerGroups);
    const initialPlayerCount = initialPlayerAttackOrder.length;
    const maxEnemyTurns = state.enemyGroupCount;

    // Track which specific groups have attacked this phase using unit names + counts as unique IDs
    const playerGroupsAttacked = new Set<string>();
    let enemyTurnsTaken = 0;

    const totalTurns = Math.max(initialPlayerCount, maxEnemyTurns);

    // Process alternating turns until all units have taken their turn
    for (let turnIndex = 0; turnIndex < totalTurns * 2; turnIndex++) {
      // Check if battle ended
      if (this.shouldBattleEnd(state)) {
        break;
      }

      // Determine whose turn it is based on initiative and turn index
      const isPlayerTurn = playerGoesFirst ? (turnIndex % 2 === 0) : (turnIndex % 2 === 1);

      if (isPlayerTurn) {
        // Player's turn - find the next player group that hasn't attacked yet
        const nextPlayerGroup = this.getNextPlayerAttacker(state, initialPlayerAttackOrder, playerGroupsAttacked);
        if (nextPlayerGroup) {
          this.processSinglePlayerAttackByGroup(state, nextPlayerGroup, playerGroupsAttacked.size);
          // Mark this group as having attacked using a unique identifier
          const groupId = `${nextPlayerGroup.unitName}:${nextPlayerGroup.count}:${nextPlayerGroup.unitStrength}`;
          playerGroupsAttacked.add(groupId);
        }
      } else {
        // Enemy's turn - check if we still have enemy units that haven't taken their turn
        if (enemyTurnsTaken < state.enemyGroupCount && state.playerGroups.length > 0) {
          this.processSingleEnemyAttack(state, enemyTurnsTaken);
          enemyTurnsTaken++;
        }
      }

      // If both sides have taken all their turns, end the battle phase
      if (playerGroupsAttacked.size >= initialPlayerCount && enemyTurnsTaken >= maxEnemyTurns) {
        break;
      }
    }
  }





  /**
   * Find the next player group that should attack based on initial attack order
   */
  private getNextPlayerAttacker(
    state: BattleState,
    initialAttackOrder: StackingGroup[],
    alreadyAttacked: Set<string>
  ): StackingGroup | null {
    // Go through the initial attack order and find the first group that:
    // 1. Still exists in the current player groups
    // 2. Hasn't attacked yet this phase
    for (const originalGroup of initialAttackOrder) {
      const groupId = `${originalGroup.unitName}:${originalGroup.count}:${originalGroup.unitStrength}`;

      if (!alreadyAttacked.has(groupId)) {
        // Check if this group still exists (might have been eliminated)
        const currentGroup = state.playerGroups.find(g =>
          g.unitName === originalGroup.unitName &&
          g.unitStrength === originalGroup.unitStrength
        );

        if (currentGroup) {
          return currentGroup;
        }
      }
    }

    return null;
  }

  /**
   * Process a single player unit's turn (one attack) for a specific group
   */
  private processSinglePlayerAttackByGroup(state: BattleState, attackingGroup: StackingGroup, attackIndex: number): void {
    if (state.playerGroups.length === 0) return;

    const targetEnemyIndex = attackIndex % state.enemyGroupCount;

    // Calculate damage and create combat action
    const damage = BattleUtils.calculateDamageDealt(attackingGroup);
    const action = BattleUtils.createPlayerAttackAction(
      state.currentTurn,
      attackingGroup,
      targetEnemyIndex
    );

    // Add to combat log and track damage
    state.combatLog.push(action);
    state.totalDamageDealt += damage;
  }

  /**
   * Process a single player unit's turn (one attack) - DEPRECATED, use processSinglePlayerAttackByGroup
   */
  private processSinglePlayerAttack(state: BattleState, attackIndex: number): void {
    if (state.playerGroups.length === 0) return;

    // Get current attack order (highest strength first) - recalculated each time
    const currentAttackOrder = this.calculateAttackOrder(state.playerGroups);

    if (attackIndex >= currentAttackOrder.length) return;

    const attackingGroup = currentAttackOrder[attackIndex];
    const targetEnemyIndex = attackIndex % state.enemyGroupCount;

    // Calculate damage and create combat action
    const damage = BattleUtils.calculateDamageDealt(attackingGroup);
    const action = BattleUtils.createPlayerAttackAction(
      state.currentTurn,
      attackingGroup,
      targetEnemyIndex
    );

    // Add to combat log and track damage
    state.combatLog.push(action);
    state.totalDamageDealt += damage;
  }

  /**
   * Process a single enemy unit's turn (one attack)
   */
  private processSingleEnemyAttack(state: BattleState, enemyGroupIndex: number): void {
    if (state.playerGroups.length === 0) return;

    // Get current targeting order for remaining player groups (highest health first)
    const currentTargetOrder = this.calculateEnemyTargetOrder(state.playerGroups);

    // Enemy always targets the highest health player group (index 0)
    if (currentTargetOrder.length === 0) return;

    const targetGroup = currentTargetOrder[0]; // Always target highest health group

    // Create combat action
    const action = BattleUtils.createEnemyAttackAction(
      state.currentTurn,
      enemyGroupIndex, // Which enemy group is attacking
      targetGroup
    );

    // Add to combat log
    state.combatLog.push(action);

    // Remove the eliminated group from player groups
    const groupIndex = state.playerGroups.findIndex(
      group => group.unitName === targetGroup.unitName &&
               group.count === targetGroup.count &&
               group.totalHealth === targetGroup.totalHealth
    );

    if (groupIndex !== -1) {
      state.playerGroups.splice(groupIndex, 1);
    }

    // Check if battle should end
    if (state.playerGroups.length === 0) {
      state.battleEnded = true;
    }
  }
}
