/**
 * Mobile performance optimization utilities
 */

import { mobileLayoutManager } from './mobileUtils';

export interface PerformanceMetrics {
  renderTime: number;
  interactionTime: number;
  scrollPerformance: number;
  memoryUsage?: number;
}

/**
 * Mobile Performance Manager
 */
export class MobilePerformanceManager {
  private static metrics: PerformanceMetrics = {
    renderTime: 0,
    interactionTime: 0,
    scrollPerformance: 0
  };

  private static observers: Map<string, IntersectionObserver> = new Map();
  private static rafId: number | null = null;

  /**
   * Initialize performance monitoring
   */
  static initialize(): void {
    if (!mobileLayoutManager.isMobile()) return;

    this.setupLazyLoading();
    this.optimizeScrolling();
    this.monitorInteractions();
    this.setupMemoryMonitoring();
  }

  /**
   * Setup lazy loading for non-critical sections
   */
  private static setupLazyLoading(): void {
    const lazyElements = document.querySelectorAll('[data-lazy]');
    
    if (lazyElements.length === 0) return;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            const element = entry.target as HTMLElement;
            this.loadElement(element);
            observer.unobserve(element);
          }
        });
      },
      {
        rootMargin: '50px',
        threshold: 0.1
      }
    );

    lazyElements.forEach(element => observer.observe(element));
    this.observers.set('lazy-loading', observer);
  }

  /**
   * Load a lazy element
   */
  private static loadElement(element: HTMLElement): void {
    const startTime = performance.now();

    // Simulate loading content
    const content = element.dataset.lazy;
    if (content) {
      element.innerHTML = content;
      element.removeAttribute('data-lazy');
    }

    const endTime = performance.now();
    this.metrics.renderTime += endTime - startTime;
  }

  /**
   * Optimize scrolling performance
   */
  private static optimizeScrolling(): void {
    const scrollableElements = document.querySelectorAll('.combat-log, .unit-family-content, .main-content');
    
    scrollableElements.forEach(element => {
      let isScrolling = false;
      let scrollTimeout: number;

      element.addEventListener('scroll', () => {
        if (!isScrolling) {
          isScrolling = true;
          this.requestOptimizedFrame(() => {
            // Optimize scroll performance
            this.optimizeScrollFrame(element as HTMLElement);
            isScrolling = false;
          });
        }

        // Clear timeout and set a new one
        clearTimeout(scrollTimeout);
        scrollTimeout = window.setTimeout(() => {
          this.onScrollEnd(element as HTMLElement);
        }, 150);
      }, { passive: true });
    });
  }

  /**
   * Optimize scroll frame
   */
  private static optimizeScrollFrame(element: HTMLElement): void {
    const startTime = performance.now();

    // Hide elements that are far out of view to improve performance
    const rect = element.getBoundingClientRect();
    const children = element.children;

    for (let i = 0; i < children.length; i++) {
      const child = children[i] as HTMLElement;
      const childRect = child.getBoundingClientRect();
      
      // Hide elements that are more than 2 viewport heights away
      const isVisible = childRect.bottom > -window.innerHeight * 2 && 
                       childRect.top < window.innerHeight * 3;
      
      if (!isVisible && !child.classList.contains('scroll-hidden')) {
        child.classList.add('scroll-hidden');
        child.style.visibility = 'hidden';
      } else if (isVisible && child.classList.contains('scroll-hidden')) {
        child.classList.remove('scroll-hidden');
        child.style.visibility = 'visible';
      }
    }

    const endTime = performance.now();
    this.metrics.scrollPerformance += endTime - startTime;
  }

  /**
   * Handle scroll end
   */
  private static onScrollEnd(element: HTMLElement): void {
    // Restore visibility to all hidden elements
    const hiddenElements = element.querySelectorAll('.scroll-hidden');
    hiddenElements.forEach(el => {
      el.classList.remove('scroll-hidden');
      (el as HTMLElement).style.visibility = 'visible';
    });
  }

  /**
   * Request optimized animation frame
   */
  private static requestOptimizedFrame(callback: () => void): void {
    if (this.rafId) {
      cancelAnimationFrame(this.rafId);
    }

    this.rafId = requestAnimationFrame(() => {
      callback();
      this.rafId = null;
    });
  }

  /**
   * Monitor touch interactions
   */
  private static monitorInteractions(): void {
    let interactionStart: number;

    document.addEventListener('touchstart', () => {
      interactionStart = performance.now();
    }, { passive: true });

    document.addEventListener('touchend', () => {
      if (interactionStart) {
        const interactionTime = performance.now() - interactionStart;
        this.metrics.interactionTime = Math.max(this.metrics.interactionTime, interactionTime);
      }
    }, { passive: true });
  }

  /**
   * Setup memory monitoring
   */
  private static setupMemoryMonitoring(): void {
    if ('memory' in performance) {
      setInterval(() => {
        const memory = (performance as any).memory;
        this.metrics.memoryUsage = memory.usedJSHeapSize / memory.jsHeapSizeLimit;
        
        // Warn if memory usage is high
        if (this.metrics.memoryUsage > 0.8) {
          console.warn('High memory usage detected:', this.metrics.memoryUsage);
          this.optimizeMemory();
        }
      }, 10000); // Check every 10 seconds
    }
  }

  /**
   * Optimize memory usage
   */
  private static optimizeMemory(): void {
    // Clear unused observers
    this.observers.forEach((observer, key) => {
      if (key !== 'lazy-loading') {
        observer.disconnect();
        this.observers.delete(key);
      }
    });

    // Force garbage collection if available
    if ('gc' in window) {
      (window as any).gc();
    }
  }

  /**
   * Get performance metrics
   */
  static getMetrics(): PerformanceMetrics {
    return { ...this.metrics };
  }

  /**
   * Reset metrics
   */
  static resetMetrics(): void {
    this.metrics = {
      renderTime: 0,
      interactionTime: 0,
      scrollPerformance: 0
    };
  }

  /**
   * Add CSS optimizations for mobile
   */
  static addMobileCSSOptimizations(): void {
    if (!mobileLayoutManager.isMobile()) return;

    const style = document.createElement('style');
    style.textContent = `
      /* Mobile Performance Optimizations */
      * {
        -webkit-tap-highlight-color: transparent;
      }

      .scroll-hidden {
        visibility: hidden !important;
      }

      /* Optimize animations for mobile */
      @media (max-width: 767px) {
        * {
          animation-duration: 0.2s !important;
          transition-duration: 0.2s !important;
        }

        /* Reduce motion for better performance */
        @media (prefers-reduced-motion: reduce) {
          * {
            animation-duration: 0.01ms !important;
            animation-iteration-count: 1 !important;
            transition-duration: 0.01ms !important;
          }
        }

        /* Optimize transforms */
        .unit-card,
        .tab-nav-item,
        .btn {
          will-change: transform;
          transform: translateZ(0);
        }

        /* Optimize scrolling */
        .combat-log,
        .unit-family-content,
        .main-content {
          -webkit-overflow-scrolling: touch;
          overflow-scrolling: touch;
          will-change: scroll-position;
        }
      }
    `;

    document.head.appendChild(style);
  }

  /**
   * Cleanup performance monitoring
   */
  static cleanup(): void {
    this.observers.forEach(observer => observer.disconnect());
    this.observers.clear();

    if (this.rafId) {
      cancelAnimationFrame(this.rafId);
      this.rafId = null;
    }
  }
}

// Auto-initialize on mobile
if (mobileLayoutManager.isMobile()) {
  document.addEventListener('DOMContentLoaded', () => {
    MobilePerformanceManager.initialize();
    MobilePerformanceManager.addMobileCSSOptimizations();
  });
}

// Cleanup when switching away from mobile
mobileLayoutManager.addLayoutChangeListener((mode) => {
  if (mode !== 'mobile') {
    MobilePerformanceManager.cleanup();
  } else {
    MobilePerformanceManager.initialize();
    MobilePerformanceManager.addMobileCSSOptimizations();
  }
});
