/**
 * Damage Optimization Service
 * Optimizes army compositions for maximum damage output against enemy groups
 * Uses existing battle simulation engine for accurate damage calculations
 */

import { Unit, ArmyComposition, OptimizationConstraints, UnitUtils, StackingGroup } from '../types/units';
import { BattleSimulationService } from './battleSimulation';
import { IBattleSimulationService, BattleAnalysis } from '../types/battleSimulation';
import { StackingOptimizer } from './stackingOptimizer';

/**
 * Progress callback for damage optimization
 */
export interface DamageOptimizationProgress {
  /** Current phase of optimization */
  phase: 'initializing' | 'generating' | 'evaluating' | 'finalizing';
  /** Progress percentage (0-100) */
  progress: number;
  /** Current status message */
  message: string;
  /** Number of combinations generated so far */
  combinationsGenerated?: number;
  /** Total combinations to generate */
  totalCombinations?: number;
  /** Number of combinations evaluated so far */
  combinationsEvaluated?: number;
  /** Total combinations to evaluate */
  totalToEvaluate?: number;
  /** Elapsed time in milliseconds */
  elapsedMs: number;
  /** Estimated time remaining in milliseconds */
  estimatedRemainingMs?: number;
}

/**
 * Configuration for damage optimization
 */
export interface DamageOptimizationConstraints extends OptimizationConstraints {
  /** Number of enemy groups to optimize against */
  enemyGroupCount: number;
  /** Maximum number of army combinations to evaluate (performance limit) */
  maxCombinations?: number;
  /** Progress callback function */
  onProgress?: (progress: DamageOptimizationProgress) => void;
  /** Cancellation signal */
  signal?: AbortSignal;
}

/**
 * Result of damage optimization for a single army composition
 */
export interface DamageOptimizationResult {
  /** The optimized army composition */
  armyComposition: ArmyComposition;
  /** Battle analysis results from simulation */
  battleAnalysis: BattleAnalysis;
  /** Total silver cost for this army */
  totalSilverCost: number;
  /** Total food consumption for this army */
  totalFoodConsumption: number;
  /** Total revival cost (silver) for this army */
  totalRevivalCost: number;
  /** Average total damage per battle across best/worst case scenarios */
  averageDamagePerBattle: number;
  /** Damage efficiency score (damage per resource cost) */
  damageEfficiencyScore: number;
}

/**
 * Complete results from damage optimization showing multiple ranked options
 */
export interface DamageOptimizationResults {
  /** Array of optimization results ranked by damage output */
  rankedResults: DamageOptimizationResult[];
  /** Total number of combinations evaluated */
  combinationsEvaluated: number;
  /** Time taken for optimization in milliseconds */
  optimizationTimeMs: number;
  /** Algorithm used for optimization */
  algorithmUsed: string;
  /** Whether optimization was truncated due to performance limits */
  wasTruncated: boolean;
}

/**
 * Interface for pluggable optimization algorithms
 */
export interface IDamageOptimizationAlgorithm {
  /** Name of the algorithm for reporting */
  name: string;

  /**
   * Generate army combinations to evaluate
   * @param constraints Optimization constraints including available units and budgets
   * @param units Array of all available units
   * @returns Promise of array of army compositions to evaluate
   */
  generateCombinations(
    constraints: DamageOptimizationConstraints,
    units: Unit[]
  ): Promise<ArmyComposition[]>;
}

/**
 * Main damage optimization service
 */
export class DamageOptimizer {
  private battleSimulator: IBattleSimulationService;
  private algorithm: IDamageOptimizationAlgorithm;

  constructor(
    battleSimulator?: IBattleSimulationService,
    algorithm?: IDamageOptimizationAlgorithm
  ) {
    this.battleSimulator = battleSimulator || new BattleSimulationService();
    this.algorithm = algorithm || new SystematicCombinationAlgorithm();
  }

  /**
   * Set the optimization algorithm to use
   */
  setAlgorithm(algorithm: IDamageOptimizationAlgorithm): void {
    this.algorithm = algorithm;
  }

  /**
   * Report progress to the callback if provided
   */
  private reportProgress(constraints: DamageOptimizationConstraints, progress: DamageOptimizationProgress): void {
    if (constraints.onProgress) {
      constraints.onProgress(progress);
    }
  }

  /**
   * Check if the operation has been cancelled
   */
  private checkCancellation(constraints: DamageOptimizationConstraints): void {
    if (constraints.signal?.aborted) {
      throw new Error('Operation was cancelled by user');
    }
  }

  /**
   * Optimize army composition for maximum damage output
   */
  async optimizeForDamage(
    constraints: DamageOptimizationConstraints,
    units: Unit[]
  ): Promise<DamageOptimizationResults> {
    const startTime = performance.now();
    const maxTimeoutMs = 120000; // 2 minutes maximum

    console.log(`üéØ Starting damage optimization with ${this.algorithm.name}`);
    console.log(`üìä Constraints: L:${constraints.leadershipBudget} D:${constraints.dominanceBudget} vs ${constraints.enemyGroupCount} enemies`);
    console.log(`‚è±Ô∏è Maximum processing time: ${maxTimeoutMs / 1000} seconds`);

    // Report initial progress
    this.reportProgress(constraints, {
      phase: 'initializing',
      progress: 0,
      message: 'Initializing damage optimizer...',
      elapsedMs: 0
    });

    // Validate inputs
    this.validateOptimizationConstraints(constraints);

    // Report generation phase start
    this.reportProgress(constraints, {
      phase: 'generating',
      progress: 10,
      message: 'Generating army combinations...',
      elapsedMs: performance.now() - startTime
    });

    // Generate army combinations using selected algorithm
    const armyCombinations = await this.algorithm.generateCombinations(constraints, units);

    console.log(`üîÑ Generated ${armyCombinations.length} army combinations to evaluate`);

    // Report evaluation phase start
    this.reportProgress(constraints, {
      phase: 'evaluating',
      progress: 20,
      message: 'Evaluating army combinations...',
      combinationsGenerated: armyCombinations.length,
      totalCombinations: armyCombinations.length,
      combinationsEvaluated: 0,
      totalToEvaluate: armyCombinations.length,
      elapsedMs: performance.now() - startTime
    });

    // Evaluate each combination with UI yielding and timeout protection
    const results: DamageOptimizationResult[] = [];
    let combinationsEvaluated = 0;

    for (let i = 0; i < armyCombinations.length; i++) {
      // Check for cancellation
      this.checkCancellation(constraints);

      // Check for timeout
      const currentTime = performance.now();
      if (currentTime - startTime > maxTimeoutMs) {
        console.warn(`‚è±Ô∏è Optimization timeout after ${(currentTime - startTime) / 1000}s - stopping at ${combinationsEvaluated} combinations`);
        break;
      }

      const army = armyCombinations[i];

      try {
        const result = await this.evaluateArmyComposition(army, constraints.enemyGroupCount, units);
        results.push(result);
        combinationsEvaluated++;
      } catch (error) {
        console.warn(`‚ö†Ô∏è Failed to evaluate army composition:`, error);
        // Continue with other combinations
      }

      // Yield control to UI every 3 combinations to prevent blocking (outside try-catch)
      if (combinationsEvaluated % 3 === 0) {
        await new Promise(resolve => setTimeout(resolve, 0));
      }

      // Report progress every 10 combinations or at key milestones
      if (combinationsEvaluated % 10 === 0 || combinationsEvaluated === armyCombinations.length) {
        const elapsedMs = performance.now() - startTime;
        const progressPercent = 20 + Math.floor((combinationsEvaluated / armyCombinations.length) * 60); // 20-80%
        const estimatedRemainingMs = combinationsEvaluated > 0
          ? (elapsedMs / combinationsEvaluated) * (armyCombinations.length - combinationsEvaluated)
          : undefined;

        this.reportProgress(constraints, {
          phase: 'evaluating',
          progress: progressPercent,
          message: `Evaluating combinations... (${combinationsEvaluated}/${armyCombinations.length})`,
          combinationsGenerated: armyCombinations.length,
          totalCombinations: armyCombinations.length,
          combinationsEvaluated,
          totalToEvaluate: armyCombinations.length,
          elapsedMs,
          estimatedRemainingMs
        });
      }

      // Additional safety: Log progress every 25 combinations
      if (combinationsEvaluated % 25 === 0) {
        const elapsed = (performance.now() - startTime) / 1000;
        console.log(`üìä Progress: ${combinationsEvaluated}/${armyCombinations.length} combinations (${elapsed.toFixed(1)}s elapsed)`);
      }
    }

    // Report finalizing phase
    this.reportProgress(constraints, {
      phase: 'finalizing',
      progress: 90,
      message: 'Finalizing results...',
      combinationsGenerated: armyCombinations.length,
      totalCombinations: armyCombinations.length,
      combinationsEvaluated,
      totalToEvaluate: armyCombinations.length,
      elapsedMs: performance.now() - startTime
    });

    // Sort results by damage output (descending)
    const rankedResults = results.sort((a, b) => {
      // Primary sort: average damage per battle
      const damageDiff = b.averageDamagePerBattle - a.averageDamagePerBattle;
      if (Math.abs(damageDiff) > 0.01) return damageDiff;

      // Secondary sort: damage efficiency score
      return b.damageEfficiencyScore - a.damageEfficiencyScore;
    });

    const endTime = performance.now();
    const optimizationTimeMs = endTime - startTime;

    console.log(`‚úÖ Optimization complete: ${rankedResults.length} valid results in ${optimizationTimeMs.toFixed(2)}ms`);

    // Report completion
    this.reportProgress(constraints, {
      phase: 'finalizing',
      progress: 100,
      message: 'Optimization complete!',
      combinationsGenerated: armyCombinations.length,
      totalCombinations: armyCombinations.length,
      combinationsEvaluated,
      totalToEvaluate: armyCombinations.length,
      elapsedMs: optimizationTimeMs
    });

    return {
      rankedResults,
      combinationsEvaluated,
      optimizationTimeMs,
      algorithmUsed: this.algorithm.name,
      wasTruncated: armyCombinations.length > (constraints.maxCombinations || 50)
    };
  }

  /**
   * Evaluate a single army composition for damage optimization
   */
  private async evaluateArmyComposition(
    army: ArmyComposition,
    enemyGroupCount: number,
    units: Unit[]
  ): Promise<DamageOptimizationResult> {
    // Run battle simulation
    const battleAnalysis = this.battleSimulator.simulateBothScenarios(army, enemyGroupCount);

    // Calculate cost metrics
    const totalSilverCost = this.calculateSilverCost(army, units);
    const totalFoodConsumption = this.calculateFoodConsumption(army, units);
    const totalRevivalCost = this.calculateRevivalCost(army, units);

    // Calculate damage metrics - total damage per battle (not per turn)
    const bestCaseTotalDamage = battleAnalysis.bestCase.totalDamageDealtToEnemies;
    const worstCaseTotalDamage = battleAnalysis.worstCase.totalDamageDealtToEnemies;

    const averageDamagePerBattle = (bestCaseTotalDamage + worstCaseTotalDamage) / 2;

    // Calculate damage efficiency (damage per resource cost)
    const totalResourceCost = army.totalLeadershipCost + army.totalDominanceCost + totalSilverCost;
    const damageEfficiencyScore = totalResourceCost > 0 ? averageDamagePerBattle / totalResourceCost : 0;

    return {
      armyComposition: army,
      battleAnalysis,
      totalSilverCost,
      totalFoodConsumption,
      totalRevivalCost,
      averageDamagePerBattle,
      damageEfficiencyScore
    };
  }

  /**
   * Calculate total silver cost for an army composition
   */
  private calculateSilverCost(army: ArmyComposition, units: Unit[]): number {
    const unitsByName = new Map(units.map(unit => [unit.name, unit]));
    let totalSilverCost = 0;

    for (const [unitName, quantity] of Object.entries(army.units)) {
      const unit = unitsByName.get(unitName);
      if (unit) {
        // Silver cost is the revival cost after defending
        totalSilverCost += unit.revival_cost_silver * quantity;
      }
    }

    return totalSilverCost;
  }

  /**
   * Calculate total food consumption for an army composition
   */
  private calculateFoodConsumption(army: ArmyComposition, units: Unit[]): number {
    const unitsByName = new Map(units.map(unit => [unit.name, unit]));
    let totalFoodConsumption = 0;

    for (const [unitName, quantity] of Object.entries(army.units)) {
      const unit = unitsByName.get(unitName);
      if (unit) {
        totalFoodConsumption += unit.food_consumption * quantity;
      }
    }

    return totalFoodConsumption;
  }

  /**
   * Calculate total revival cost (silver) for an army composition
   */
  private calculateRevivalCost(army: ArmyComposition, units: Unit[]): number {
    // Revival cost is the same as silver cost in this context
    return this.calculateSilverCost(army, units);
  }

  /**
   * Validate optimization constraints
   */
  private validateOptimizationConstraints(constraints: DamageOptimizationConstraints): void {
    if (constraints.enemyGroupCount < 1 || constraints.enemyGroupCount > 100) {
      throw new Error('Enemy group count must be between 1 and 100');
    }

    if (constraints.leadershipBudget < 0) {
      throw new Error('Leadership budget cannot be negative');
    }

    if (constraints.dominanceBudget < 0) {
      throw new Error('Dominance budget cannot be negative');
    }

    if (!constraints.availableUnits || constraints.availableUnits.length === 0) {
      throw new Error('At least one unit type must be available for optimization');
    }

    if (constraints.maxCombinations && constraints.maxCombinations < 1) {
      throw new Error('Maximum combinations must be at least 1');
    }
  }
}

/**
 * Systematic combination algorithm that tests all possible unit combinations
 * Uses the proven stacking algorithm for each combination
 */
export class SystematicCombinationAlgorithm implements IDamageOptimizationAlgorithm {
  name = 'Systematic Combination Testing';

  async generateCombinations(
    constraints: DamageOptimizationConstraints,
    units: Unit[]
  ): Promise<ArmyComposition[]> {
    const generationStartTime = performance.now();
    const maxGenerationTimeMs = 60000; // 1 minute for generation phase

    console.log(`üîç Generating combinations using systematic testing algorithm`);

    // Filter units to only those available for optimization
    const availableUnits = units.filter(unit =>
      constraints.availableUnits.includes(unit.name)
    );

    console.log(`üìã Available units for optimization: ${availableUnits.length}`);

    // Generate unit combinations with user-specified limit
    const maxCombinations = constraints.maxCombinations || 50;
    const unitCombinations = this.generateUnitCombinations(availableUnits.map(u => u.name), maxCombinations);
    console.log(`üîÑ Testing ${unitCombinations.length} different unit combinations (user requested: ${maxCombinations})`);

    const results: ArmyComposition[] = [];

    // Test each combination using the proven stacking algorithm
    for (let i = 0; i < unitCombinations.length; i++) {
      // Check for timeout during generation
      const currentTime = performance.now();
      if (currentTime - generationStartTime > maxGenerationTimeMs) {
        console.warn(`‚è±Ô∏è Generation timeout after ${(currentTime - generationStartTime) / 1000}s - stopping at ${i} combinations`);
        break;
      }

      const combination = unitCombinations[i];

      try {
        const army = await this.testCombinationWithStackingAlgorithm(combination, constraints, units);
        if (army) {
          results.push(army);
        }
      } catch (error) {
        console.warn(`‚ö†Ô∏è Failed to test combination [${combination.join(', ')}]:`, error);
      }

      // Yield control to UI every 5 combinations during generation (outside try-catch)
      if (i % 5 === 0) {
        await new Promise(resolve => setTimeout(resolve, 0));
      }

      // Log progress every 20 combinations during generation
      if (i % 20 === 0 && i > 0) {
        const elapsed = (performance.now() - generationStartTime) / 1000;
        console.log(`üîÑ Generation progress: ${i}/${unitCombinations.length} combinations tested (${elapsed.toFixed(1)}s)`);
      }
    }

    console.log(`‚úÖ Generated ${results.length} valid army combinations for evaluation`);
    return results;
  }

  /**
   * Generate unit combinations with user-specified limit and time-based protection
   */
  private generateUnitCombinations(unitNames: string[], maxCombinations: number): string[][] {
    // Calculate total possible combinations
    const totalPossible = Math.pow(2, unitNames.length) - 1; // -1 to exclude empty set
    console.log(`üìä Total possible combinations: ${totalPossible}, user requested: ${maxCombinations}`);

    // If we have too many possible combinations, use a smarter approach
    if (totalPossible > maxCombinations) {
      return this.generateLimitedCombinations(unitNames, maxCombinations);
    }

    // Generate all subsets (2^n combinations) if within limit, using top-down ordering
    const allCombinations: string[][] = [];

    for (let i = 1; i <= totalPossible; i++) {
      const combination: string[] = [];
      for (let j = 0; j < unitNames.length; j++) {
        if (i & (1 << j)) {
          combination.push(unitNames[j]);
        }
      }
      allCombinations.push(combination);
    }

    // Sort combinations by size (largest first) for top-down approach
    allCombinations.sort((a, b) => b.length - a.length);

    console.log(`üéØ Generated all ${allCombinations.length} combinations, ordered largest to smallest`);
    console.log(`   User requested: ${maxCombinations} combinations (all possible combinations fit within limit)`);
    return allCombinations;
  }

  /**
   * Generate a limited set of smart combinations when full power set is too large
   * Strategy: Start with all units, then systematically remove units (top-down approach)
   */
  private generateLimitedCombinations(unitNames: string[], maxCombinations: number): string[][] {
    const combinations: string[][] = [];

    // Strategy 1: Start with ALL units (most comprehensive army)
    combinations.push([...unitNames]);

    // Strategy 2: Remove one unit at a time (n-1 combinations)
    for (let i = 0; i < unitNames.length && combinations.length < maxCombinations; i++) {
      const combination = unitNames.filter((_, index) => index !== i);
      combinations.push(combination);
    }

    // Strategy 3: Remove two units at a time (n-2 combinations)
    for (let i = 0; i < unitNames.length && combinations.length < maxCombinations; i++) {
      for (let j = i + 1; j < unitNames.length && combinations.length < maxCombinations; j++) {
        const combination = unitNames.filter((_, index) => index !== i && index !== j);
        if (combination.length > 0) {
          combinations.push(combination);
        }
      }
    }

    // Strategy 4: Remove three units at a time (n-3 combinations)
    for (let i = 0; i < unitNames.length && combinations.length < maxCombinations; i++) {
      for (let j = i + 1; j < unitNames.length && combinations.length < maxCombinations; j++) {
        for (let k = j + 1; k < unitNames.length && combinations.length < maxCombinations; k++) {
          const combination = unitNames.filter((_, index) => index !== i && index !== j && index !== k);
          if (combination.length > 0) {
            combinations.push(combination);
          }
        }
      }
    }

    // Strategy 5: If we still have room, add some strategic smaller combinations
    if (combinations.length < maxCombinations) {
      // Add some mid-size combinations (half the units)
      const halfSize = Math.floor(unitNames.length / 2);
      for (let attempt = 0; attempt < 10 && combinations.length < maxCombinations; attempt++) {
        const combination = this.getRandomCombination(unitNames, halfSize);

        // Only add if we don't already have this combination
        if (!combinations.some(existing =>
          existing.length === combination.length &&
          existing.every(unit => combination.includes(unit))
        )) {
          combinations.push(combination);
        }
      }

      // Add some smaller combinations if we still have room
      const quarterSize = Math.max(1, Math.floor(unitNames.length / 4));
      for (let attempt = 0; attempt < 5 && combinations.length < maxCombinations; attempt++) {
        const combination = this.getRandomCombination(unitNames, quarterSize);

        // Only add if we don't already have this combination
        if (!combinations.some(existing =>
          existing.length === combination.length &&
          existing.every(unit => combination.includes(unit))
        )) {
          combinations.push(combination);
        }
      }
    }

    console.log(`üéØ Generated ${combinations.length} top-down combinations from ${unitNames.length} units`);
    console.log(`   Strategy: Started with all ${unitNames.length} units, then systematically removed units`);
    console.log(`   User requested: ${maxCombinations} combinations (time-based limits still apply)`);
    return combinations;
  }

  /**
   * Get a random combination of specified size
   */
  private getRandomCombination(unitNames: string[], size: number): string[] {
    const shuffled = [...unitNames].sort(() => Math.random() - 0.5);
    return shuffled.slice(0, size);
  }

  /**
   * Test a specific unit combination using the existing stacking algorithm
   */
  private async testCombinationWithStackingAlgorithm(
    unitCombination: string[],
    constraints: DamageOptimizationConstraints,
    allUnits: Unit[]
  ): Promise<ArmyComposition | null> {
    try {
      // Create stacking optimizer with all units
      const stackingOptimizer = new StackingOptimizer(allUnits);

      // Create constraints for this specific combination
      const stackingConstraints: OptimizationConstraints = {
        leadershipBudget: constraints.leadershipBudget,
        dominanceBudget: constraints.dominanceBudget,
        mercenaryLimits: constraints.mercenaryLimits,
        availableUnits: unitCombination
      };

      // Use the proven stacking algorithm
      const result = await stackingOptimizer.optimizeArmy(stackingConstraints);

      // Extract the best composition from the result
      if (result.compositions && result.compositions.length > 0) {
        return result.compositions[0]; // Return the best composition
      }

      return null;

    } catch (error) {
      console.warn(`Failed to test combination [${unitCombination.join(', ')}]:`, error);
      return null;
    }
  }


}