/**
 * Unit types and interfaces for the TotalBattle Army Calculator
 * Based on validated unit data from final_units.json
 */

export type CostType = 'Leadership' | 'Dominance' | 'Authority' | 'Mercenary';

/**
 * Base unit interface representing a TotalBattle unit from our validated data
 */
export interface Unit {
  /** Display name of the unit */
  name: string;
  /** Array of unit type tags (e.g., ['Human', 'Ranged', 'Guardsman']) */
  unit_types: string[];
  /** Primary cost type for this unit */
  cost_type: CostType;
  /** Unit health points */
  health: number;
  /** Unit attack strength */
  strength: number;
  /** Leadership cost to deploy this unit */
  leadership_cost: number;
  /** Dominance cost to deploy this unit */
  dominance_cost: number;
  /** Authority cost to deploy this unit (for mercenaries) */
  authority_cost: number;
  /** Food consumption per unit */
  food_consumption: number;
  /** Carrying capacity of the unit */
  carrying_capacity: number;
  /** Gold cost to revive this unit */
  revival_cost_gold: number;
  /** Silver cost to revive this unit */
  revival_cost_silver: number;
  /** Source screenshot file this unit was extracted from */
  source_file: string;

  // Legacy properties for backward compatibility
  id?: string;
  class?: string;
  leadershipCost?: number;
  dominanceCost?: number;
  description?: string;
}

/**
 * Unit with calculated properties for optimization
 */
export interface UnitWithCalculatedProps extends Unit {
  readonly isMercenary: boolean;
  readonly primaryCost: number;
  readonly strengthPerCost: number;
  readonly healthPerCost: number;
}

/**
 * A group of identical units in the stacking order
 */
export interface StackingGroup {
  /** Name of the unit */
  unitName: string;
  /** Number of units in this group */
  count: number;
  /** Total health of all units in this group */
  totalHealth: number;
  /** Individual unit strength */
  unitStrength: number;
}

/**
 * Complete army composition with stacking analysis
 */
export interface ArmyComposition {
  /** Map of unit names to quantities */
  units: Record<string, number>;
  /** Total army strength */
  totalStrength: number;
  /** Total army health */
  totalHealth: number;
  /** Total leadership cost */
  totalLeadershipCost: number;
  /** Total dominance cost */
  totalDominanceCost: number;
  /** Total mercenary count */
  totalMercenaryCount: number;
  /** Stacking order from weakest to strongest */
  stackingOrder: StackingGroup[];
  /** Whether the stacking is valid (weaker units have higher total health) */
  isValidStacking: boolean;
  /** Efficiency score (strength per cost with stacking bonus) */
  efficiencyScore: number;
  /** Legacy properties for backward compatibility */
  effectiveness?: number;
  totalLeadership?: number;
  totalDominance?: number;
  metadata?: any;
}

/**
 * Constraints for army optimization
 */
export interface OptimizationConstraints {
  /** Total leadership points available */
  leadershipBudget: number;
  /** Total dominance points available */
  dominanceBudget: number;
  /** Map of mercenary names to maximum quantities */
  mercenaryLimits: Record<string, number>;
  /** Array of unit names that are available for selection */
  availableUnits: string[];
  /** Legacy budget property for backward compatibility */
  budget?: { leadership: number; dominance: number };
  /** Legacy modifiers property for backward compatibility */
  modifiers?: ClassModifiers;
}

/**
 * Result of army optimization
 */
export interface OptimizationResult {
  /** Array of valid army compositions, sorted by efficiency */
  compositions: ArmyComposition[];
  /** Total number of combinations evaluated */
  totalCombinationsEvaluated: number;
  /** Number of valid stacking solutions found */
  validStackingsFound: number;
  /** Time taken to complete optimization in milliseconds */
  executionTimeMs: number;
}

/**
 * Filter criteria for units
 */
export interface UnitFilter {
  /** Filter by cost type */
  costType?: CostType;
  /** Filter by unit types (must have at least one) */
  unitTypes?: string[];
  /** Minimum strength requirement */
  minStrength?: number;
  /** Maximum cost requirement */
  maxCost?: number;
}

/**
 * Summary statistics for loaded units
 */
export interface UnitSummary {
  /** Total number of units */
  totalUnits: number;
  /** Count of units by cost type */
  byCostType: Record<CostType, number>;
  /** Strength range across all units */
  strengthRange: {
    min: number;
    max: number;
  };
  /** Health range across all units */
  healthRange: {
    min: number;
    max: number;
  };
}

/**
 * Utility class for unit operations
 */
export class UnitUtils {
  static isMercenary(unit: Unit): boolean {
    return unit.cost_type === 'Mercenary' || unit.authority_cost > 0;
  }

  static getPrimaryCost(unit: Unit): number {
    switch (unit.cost_type) {
      case 'Leadership':
        return unit.leadership_cost;
      case 'Dominance':
        return unit.dominance_cost;
      case 'Authority':
      case 'Mercenary':
        return unit.authority_cost;
      default:
        return 0;
    }
  }

  static getStrengthPerCost(unit: Unit): number {
    const cost = UnitUtils.getPrimaryCost(unit);
    return cost > 0 ? unit.strength / cost : 0;
  }

  static getHealthPerCost(unit: Unit): number {
    const cost = UnitUtils.getPrimaryCost(unit);
    return cost > 0 ? unit.health / cost : 0;
  }
}

/**
 * Constants for the application
 */
export const UNIT_TYPES = [
  'Human', 'Beast', 'Undead', 'Demon', 'Elemental', 'Construct',
  'Melee', 'Ranged', 'Magic', 'Siege', 'Mounted', 'Flying', 'Aquatic',
  'Guardsman', 'Monster', 'Mercenary', 'Specialist', 'Elite', 'Heavy', 'Light'
] as const;

export const COST_TYPES: CostType[] = ['Leadership', 'Dominance', 'Authority', 'Mercenary'];

export const DEFAULT_OPTIMIZATION_CONSTRAINTS: OptimizationConstraints = {
  leadershipBudget: 0,
  dominanceBudget: 0,
  mercenaryLimits: {},
  availableUnits: []
};

// Legacy type aliases for backward compatibility
export type UnitClass = string;
export type AvailableUnit = Unit;
export type ArmyUnit = { unit: Unit; quantity: number; position?: number };
export type ResourceBudget = { leadership: number; dominance: number };
export type ClassModifiers = Record<string, number>;
export type ArmyCalculationInput = OptimizationConstraints;
export type ValidationResult = { isValid: boolean; errors: string[]; warnings?: string[] };
export type SavedArmy = ArmyComposition;
