/**
 * Main entry point for the TotalBattle Army Calculator
 */

import './style.css';
import { ArmyCalculatorApp } from './components/ArmyCalculator/ArmyCalculatorApp';

// Initialize the application
document.addEventListener('DOMContentLoaded', () => {
  const appContainer = document.getElementById('app');
  
  if (!appContainer) {
    throw new Error('App container not found');
  }

  // Create and mount the main application
  const app = new ArmyCalculatorApp();
  app.mount(appContainer);

  // Add global error handling
  window.addEventListener('error', (event) => {
    console.error('Global error:', event.error);
    // You could add error reporting here
  });

  window.addEventListener('unhandledrejection', (event) => {
    console.error('Unhandled promise rejection:', event.reason);
    // You could add error reporting here
  });

  // Add service worker registration for PWA (future enhancement)
  if ('serviceWorker' in navigator) {
    window.addEventListener('load', () => {
      // Service worker registration would go here
      console.log('Service worker support detected');
    });
  }

  console.log('TotalBattle Army Calculator initialized');
});
