/**
 * Responsive navigation manager for hybrid layout approach
 * Handles desktop, tablet, and mobile navigation patterns
 */

import { mobileLayoutManager, LayoutMode, TouchInteractionManager } from './mobileUtils';

export interface NavigationSection {
  id: string;
  title: string;
  icon: string;
  element: HTMLElement | null;
  isAvailable: boolean;  // Whether the section has content and can be accessed
  isCollapsed?: boolean;
}

export class ResponsiveNavigationManager {
  private sections: NavigationSection[] = [];
  private currentActiveSection: string | null = null;
  private tabContainer: HTMLElement | null = null;
  private initialized = false;

  constructor() {
    this.setupLayoutListener();
  }

  /**
   * Initialize the navigation system
   */
  public initialize(): void {
    if (this.initialized) return;

    this.identifySections();
    this.createNavigationElements();
    this.setupInitialLayout();
    this.initialized = true;
  }

  /**
   * Identify main sections in the app
   */
  private identifySections(): void {
    this.sections = [
      {
        id: 'config-section',
        title: 'Configuration',
        icon: '‚öôÔ∏è',
        element: document.getElementById('config-section'),
        isAvailable: true,
        isCollapsed: false
      },
      {
        id: 'results-section',
        title: 'Results',
        icon: 'üéØ',
        element: document.getElementById('results-section'),
        isAvailable: false,
        isCollapsed: false
      },
      {
        id: 'battle-simulation-container',
        title: 'Battle Simulation',
        icon: '‚öîÔ∏è',
        element: document.getElementById('battle-simulation-container'),
        isAvailable: false,
        isCollapsed: false
      }
    ];
  }

  /**
   * Create navigation elements for different layouts
   */
  private createNavigationElements(): void {
    this.createMobileTabNavigation();
    this.createTabletCollapsibleHeaders();
  }

  /**
   * Create mobile tab navigation
   */
  private createMobileTabNavigation(): void {
    const mainContent = document.querySelector('.main-content');
    if (!mainContent) return;

    // Create tab container
    const tabContainer = document.createElement('div');
    tabContainer.className = 'mobile-tab-navigation mobile-only';
    tabContainer.innerHTML = `
      <div class="tab-nav-container">
        ${this.sections.map(section => `
          <button class="tab-nav-item ${section.id === 'config-section' ? 'active' : ''}" 
                  data-section="${section.id}">
            <span class="tab-icon">${section.icon}</span>
            <span class="tab-label">${section.title}</span>
          </button>
        `).join('')}
      </div>
    `;

    // Insert before main content
    mainContent.parentNode?.insertBefore(tabContainer, mainContent);
    this.tabContainer = tabContainer;

    // Add event listeners
    this.attachTabListeners();

    // Add swipe support for mobile
    this.addSwipeSupport();
  }

  /**
   * Create tablet collapsible headers
   */
  private createTabletCollapsibleHeaders(): void {
    this.sections.forEach(section => {
      if (!section.element) return;

      // Create collapsible header
      const header = document.createElement('div');
      header.className = 'collapsible-header tablet-only';
      header.innerHTML = `
        <div class="collapsible-title">
          <span class="section-icon">${section.icon}</span>
          <h2>${section.title}</h2>
          <span class="collapse-indicator">‚ñº</span>
        </div>
      `;

      // Insert header before section content
      section.element.insertBefore(header, section.element.firstChild);

      // Add click listener
      header.addEventListener('click', () => {
        this.toggleSectionCollapse(section.id);
      });
    });
  }

  /**
   * Setup initial layout based on current screen size
   */
  private setupInitialLayout(): void {
    const currentMode = mobileLayoutManager.getCurrentMode();
    this.applyLayoutMode(currentMode);

    // Initialize tab visibility
    this.updateTabVisibility();
  }

  /**
   * Apply layout mode
   */
  private applyLayoutMode(mode: LayoutMode): void {
    switch (mode) {
      case 'mobile':
        this.applyMobileLayout();
        break;
      case 'tablet':
        this.applyTabletLayout();
        break;
      case 'desktop':
        this.applyDesktopLayout();
        break;
    }
  }

  /**
   * Apply mobile layout (tab navigation)
   */
  private applyMobileLayout(): void {
    // Show only active section, hide others (but don't change isVisible for available sections)
    this.sections.forEach(section => {
      if (!section.element) return;

      if (section.id === this.currentActiveSection ||
          (this.currentActiveSection === null && section.id === 'config-section')) {
        section.element.classList.remove('hidden');
        // Don't change isVisible - it tracks actual availability, not display state
      } else {
        section.element.classList.add('hidden');
        // Don't change isVisible - it tracks actual availability, not display state
      }
    });

    // Update tab active state
    this.updateTabActiveState();
  }

  /**
   * Apply tablet layout (collapsible sections)
   */
  private applyTabletLayout(): void {
    // Show all available sections but allow collapsing
    this.sections.forEach(section => {
      if (!section.element) return;

      // Only show sections that are available
      if (section.isAvailable) {
        section.element.classList.remove('hidden');
      } else {
        section.element.classList.add('hidden');
      }

      // Apply collapsed state if needed
      if (section.isCollapsed) {
        section.element.classList.add('collapsed');
      } else {
        section.element.classList.remove('collapsed');
      }
    });
  }

  /**
   * Apply desktop layout (current layout)
   */
  private applyDesktopLayout(): void {
    // Show all available sections, remove any mobile/tablet specific classes
    this.sections.forEach(section => {
      if (!section.element) return;

      // Only show sections that are available
      if (section.isAvailable) {
        section.element.classList.remove('hidden', 'collapsed');
      } else {
        section.element.classList.add('hidden');
      }
      section.isCollapsed = false;
    });
  }

  /**
   * Switch to section (mobile)
   */
  public switchToSection(sectionId: string): void {
    this.currentActiveSection = sectionId;

    if (mobileLayoutManager.isMobile()) {
      this.applyMobileLayout();
    }
  }

  /**
   * Toggle section collapse (tablet)
   */
  private toggleSectionCollapse(sectionId: string): void {
    const section = this.sections.find(s => s.id === sectionId);
    if (!section || !section.element) return;

    section.isCollapsed = !section.isCollapsed;
    
    if (section.isCollapsed) {
      section.element.classList.add('collapsed');
    } else {
      section.element.classList.remove('collapsed');
    }

    // Update collapse indicator
    const indicator = section.element.querySelector('.collapse-indicator');
    if (indicator) {
      indicator.textContent = section.isCollapsed ? '‚ñ∂' : '‚ñº';
    }
  }

  /**
   * Attach tab navigation listeners
   */
  private attachTabListeners(): void {
    if (!this.tabContainer) return;

    const tabItems = this.tabContainer.querySelectorAll('.tab-nav-item');
    tabItems.forEach(tab => {
      tab.addEventListener('click', () => {
        // Don't switch if tab is disabled
        if (tab.classList.contains('disabled')) return;

        const sectionId = tab.getAttribute('data-section');
        if (sectionId) {
          this.switchToSection(sectionId);
        }
      });
    });
  }

  /**
   * Update tab active state
   */
  private updateTabActiveState(): void {
    if (!this.tabContainer) return;

    const tabItems = this.tabContainer.querySelectorAll('.tab-nav-item');
    tabItems.forEach(tab => {
      const sectionId = tab.getAttribute('data-section');
      if (sectionId === this.currentActiveSection || 
          (this.currentActiveSection === null && sectionId === 'config-section')) {
        tab.classList.add('active');
      } else {
        tab.classList.remove('active');
      }
    });
  }

  /**
   * Setup layout change listener
   */
  private setupLayoutListener(): void {
    mobileLayoutManager.addLayoutChangeListener((mode) => {
      if (this.initialized) {
        this.applyLayoutMode(mode);
      }
    });
  }

  /**
   * Show section (called when results are ready)
   */
  public showSection(sectionId: string): void {
    const section = this.sections.find(s => s.id === sectionId);
    if (!section) return;

    // Refresh element reference if it's null
    if (!section.element) {
      section.element = document.getElementById(sectionId);
      if (!section.element) return;
    }

    // Remove hidden class
    section.element.classList.remove('hidden');
    section.isAvailable = true;

    // If mobile, switch to this section
    if (mobileLayoutManager.isMobile()) {
      this.switchToSection(sectionId);
    }

    // Update tab navigation to show this section is available
    this.updateTabVisibility();
  }

  /**
   * Hide section
   */
  public hideSection(sectionId: string): void {
    const section = this.sections.find(s => s.id === sectionId);
    if (!section || !section.element) return;

    section.element.classList.add('hidden');
    section.isAvailable = false;

    // Update tab navigation
    this.updateTabVisibility();

    // If this was the active section on mobile, switch to config
    if (mobileLayoutManager.isMobile() && this.currentActiveSection === sectionId) {
      this.switchToSection('config-section');
    }
  }

  /**
   * Update tab visibility based on section availability
   */
  private updateTabVisibility(): void {
    if (!this.tabContainer) return;

    const tabItems = this.tabContainer.querySelectorAll('.tab-nav-item');
    tabItems.forEach(tab => {
      const sectionId = tab.getAttribute('data-section');
      const section = this.sections.find(s => s.id === sectionId);

      if (section) {
        if (section.isAvailable) {
          tab.classList.remove('disabled');
          (tab as HTMLButtonElement).disabled = false;
        } else {
          tab.classList.add('disabled');
          (tab as HTMLButtonElement).disabled = true;
        }
      }
    });
  }

  /**
   * Add swipe support for mobile navigation
   */
  private addSwipeSupport(): void {
    if (!mobileLayoutManager.isMobile()) return;

    const mainContent = document.querySelector('.main-content');
    if (!mainContent) return;

    TouchInteractionManager.addSwipeSupport(
      mainContent as HTMLElement,
      () => this.swipeToNextSection(), // Swipe left = next section
      () => this.swipeToPreviousSection() // Swipe right = previous section
    );
  }

  /**
   * Navigate to next available section via swipe
   */
  private swipeToNextSection(): void {
    const availableSections = this.sections.filter(s => s.isAvailable);
    const currentIndex = availableSections.findIndex(s => s.id === this.currentActiveSection);

    if (currentIndex < availableSections.length - 1) {
      const nextSection = availableSections[currentIndex + 1];
      this.switchToSection(nextSection.id);
    }
  }

  /**
   * Navigate to previous available section via swipe
   */
  private swipeToPreviousSection(): void {
    const availableSections = this.sections.filter(s => s.isAvailable);
    const currentIndex = availableSections.findIndex(s => s.id === this.currentActiveSection);

    if (currentIndex > 0) {
      const previousSection = availableSections[currentIndex - 1];
      this.switchToSection(previousSection.id);
    }
  }
}

// Global navigation manager instance
export const responsiveNavigation = new ResponsiveNavigationManager();
