/**
 * Battle simulation types and interfaces for the TotalBattle Army Calculator
 * Handles asymmetric combat where enemies never die, only players get eliminated
 */

import { ArmyComposition, StackingGroup } from './units';

/**
 * Represents a single unit's turn in the battle log
 */
export interface CombatAction {
  /** Battle phase number when this unit's turn occurred */
  turn: number;
  /** Who is taking their turn */
  attacker: string; // e.g., "23 Epic Monster Hunter VI" or "Enemy Group 1"
  /** Who is being attacked */
  target: string; // e.g., "Enemy Group 1" or "1000 Spearman I"
  /** Description of the action taken during this unit's turn */
  action: string; // e.g., "attack and kill" or "attack and deal damage"
  /** Damage dealt (for player unit turns on enemies) */
  damageDealt?: number;
  /** Whether this unit's turn eliminated the target (only for enemy turns on players) */
  eliminated: boolean;
}

/**
 * Current state of the battle
 */
export interface BattleState {
  /** Current battle phase number */
  currentTurn: number;
  /** Remaining player unit groups (gets smaller as groups are eliminated) */
  playerGroups: StackingGroup[];
  /** Number of enemy groups (never changes) */
  enemyGroupCount: number;
  /** Total damage dealt to enemies so far */
  totalDamageDealt: number;
  /** Whether the battle has ended */
  battleEnded: boolean;
  /** Combat log of all unit turns taken */
  combatLog: CombatAction[];
}

/**
 * Configuration for battle simulation
 */
export interface BattleConfiguration {
  /** Player's army composition */
  playerArmy: ArmyComposition;
  /** Number of enemy groups to fight against */
  enemyGroupCount: number;
  /** Whether player gets first attack (true = best case, false = worst case) */
  playerGoesFirst: boolean;
}

/**
 * Result of a battle simulation
 */
export interface BattleResult {
  /** Battle outcome (always player_eliminated since enemies never die) */
  outcome: 'player_eliminated';
  /** Complete combat log showing all unit turns */
  combatLog: CombatAction[];
  /** Total damage dealt to enemies by player units */
  totalDamageDealtToEnemies: number;
  /** Total number of battle phases the battle lasted */
  battleDuration: number;
  /** How many battle phases the player survived */
  playerSurvivalTurns: number;
  /** Whether this was best case (player first) or worst case (enemy first) */
  scenario: 'best_case' | 'worst_case';
  /** Original battle configuration */
  configuration: BattleConfiguration;
  /** Additional battle statistics */
  statistics: BattleStatistics;
}

/**
 * Detailed battle statistics
 */
export interface BattleStatistics {
  /** Total number of player unit turns taken */
  totalPlayerAttacks: number;
  /** Total number of enemy unit turns taken */
  totalEnemyAttacks: number;
  /** Average damage per player unit turn */
  averageDamagePerAttack: number;
  /** Damage dealt by each unit type */
  damageByUnitType: Record<string, number>;
  /** Number of turns taken by each unit type */
  attacksByUnitType: Record<string, number>;
  /** Player groups eliminated per battle phase */
  eliminationRate: number;
}

/**
 * Combined results for both best and worst case scenarios
 */
export interface BattleAnalysis {
  /** Best case scenario result (player attacks first) */
  bestCase: BattleResult;
  /** Worst case scenario result (enemy attacks first) */
  worstCase: BattleResult;
  /** Comparison metrics between scenarios */
  comparison: {
    /** Difference in damage dealt between best and worst case */
    damageDifference: number;
    /** Difference in survival turns between best and worst case */
    survivalDifference: number;
    /** Average damage across both scenarios */
    averageDamage: number;
    /** Average survival turns across both scenarios */
    averageSurvival: number;
  };
}

/**
 * Interface for the battle simulation service
 */
export interface IBattleSimulationService {
  /**
   * Simulate a single battle scenario
   */
  simulateBattle(config: BattleConfiguration): BattleResult;

  /**
   * Simulate both best and worst case scenarios
   */
  simulateBothScenarios(playerArmy: ArmyComposition, enemyGroupCount: number): BattleAnalysis;

  /**
   * Calculate the attack order for player groups (by strength)
   */
  calculateAttackOrder(groups: StackingGroup[]): StackingGroup[];

  /**
   * Calculate enemy targeting order for player groups (by health)
   */
  calculateEnemyTargetOrder(groups: StackingGroup[]): StackingGroup[];

  /**
   * Determine if the battle should end
   */
  shouldBattleEnd(state: BattleState): boolean;

  /**
   * Process a single turn of combat
   */
  processTurn(state: BattleState, playerGoesFirst: boolean): void;
}

/**
 * Utility functions for battle simulation
 */
export class BattleUtils {
  /**
   * Format a stacking group for display in combat log
   */
  static formatGroupForLog(group: StackingGroup): string {
    return `${group.count} ${group.unitName}`;
  }

  /**
   * Format an enemy group for display in combat log
   */
  static formatEnemyGroupForLog(groupIndex: number): string {
    return `Enemy Group ${groupIndex + 1}`;
  }

  /**
   * Calculate damage dealt by a player group
   */
  static calculateDamageDealt(group: StackingGroup): number {
    return group.count * group.unitStrength;
  }

  /**
   * Create a combat action for player unit's turn attacking enemy
   */
  static createPlayerAttackAction(
    battlePhase: number,
    attackerGroup: StackingGroup,
    enemyGroupIndex: number
  ): CombatAction {
    const damage = BattleUtils.calculateDamageDealt(attackerGroup);
    return {
      turn: battlePhase,
      attacker: BattleUtils.formatGroupForLog(attackerGroup),
      target: BattleUtils.formatEnemyGroupForLog(enemyGroupIndex),
      action: `attack and deal ${damage.toLocaleString()} damage`,
      damageDealt: damage,
      eliminated: false
    };
  }

  /**
   * Create a combat action for enemy unit's turn attacking player
   */
  static createEnemyAttackAction(
    battlePhase: number,
    enemyGroupIndex: number,
    targetGroup: StackingGroup
  ): CombatAction {
    return {
      turn: battlePhase,
      attacker: BattleUtils.formatEnemyGroupForLog(enemyGroupIndex),
      target: BattleUtils.formatGroupForLog(targetGroup),
      action: `attack and kill ${BattleUtils.formatGroupForLog(targetGroup)}`,
      eliminated: true
    };
  }

  /**
   * Format combat log for display
   */
  static formatCombatLogForDisplay(combatLog: CombatAction[]): string[] {
    return combatLog.map(action => {
      if (action.eliminated) {
        return `${action.attacker} ${action.action}`;
      } else {
        return `${action.attacker} ${action.action}`;
      }
    });
  }

  /**
   * Calculate detailed battle statistics from combat log
   */
  static calculateBattleStatistics(combatLog: CombatAction[], totalDamage: number): any { // Using any to avoid circular reference
    const playerAttacks = combatLog.filter(action => !action.eliminated);
    const enemyAttacks = combatLog.filter(action => action.eliminated);

    const damageByUnitType: Record<string, number> = {};
    const attacksByUnitType: Record<string, number> = {};

    // Calculate damage and attacks by unit type
    playerAttacks.forEach(action => {
      const unitType = action.attacker.split(' ').slice(1).join(' '); // Remove count, keep unit name
      const damage = action.damageDealt || 0;

      damageByUnitType[unitType] = (damageByUnitType[unitType] || 0) + damage;
      attacksByUnitType[unitType] = (attacksByUnitType[unitType] || 0) + 1;
    });

    return {
      totalPlayerAttacks: playerAttacks.length,
      totalEnemyAttacks: enemyAttacks.length,
      averageDamagePerAttack: playerAttacks.length > 0 ? totalDamage / playerAttacks.length : 0,
      damageByUnitType,
      attacksByUnitType,
      eliminationRate: enemyAttacks.length > 0 ? enemyAttacks.length / combatLog.length : 0
    };
  }

  /**
   * Get battle summary from combat log
   */
  static getBattleSummary(result: any): string { // Using any to avoid circular reference
    const stats = result.statistics || BattleUtils.calculateBattleStatistics(result.combatLog, result.totalDamageDealtToEnemies);

    return `Battle Summary:
- Duration: ${result.battleDuration} battle phases
- Player unit turns taken: ${stats.totalPlayerAttacks}
- Enemy unit turns taken: ${stats.totalEnemyAttacks}
- Total damage dealt to enemies: ${result.totalDamageDealtToEnemies.toLocaleString()}
- Average damage per unit turn: ${Math.round(stats.averageDamagePerAttack).toLocaleString()}
- Scenario: ${result.scenario === 'best_case' ? 'Best Case (Player First)' : 'Worst Case (Enemy First)'}`;
  }
}

/**
 * Constants for battle simulation
 */
export const BATTLE_CONSTANTS = {
  /** Maximum number of turns before forcing battle end (safety limit) */
  MAX_BATTLE_TURNS: 1000,
  /** Default enemy group count if not specified */
  DEFAULT_ENEMY_GROUPS: 1
} as const;
