/**
 * Mobile-specific UI patterns and components
 */

import { mobileLayoutManager } from './mobileUtils';

export interface FloatingActionButtonConfig {
  icon: string;
  label: string;
  onClick: () => void;
  position?: 'bottom-right' | 'bottom-left' | 'bottom-center';
  color?: 'primary' | 'secondary' | 'success' | 'warning' | 'danger';
}

export interface BottomSheetConfig {
  title: string;
  content: string | HTMLElement;
  actions?: Array<{
    label: string;
    onClick: () => void;
    style?: 'primary' | 'secondary' | 'danger';
  }>;
  dismissible?: boolean;
}

/**
 * Mobile UI Pattern Manager
 */
export class MobileUIPatterns {
  private static fabContainer: HTMLElement | null = null;
  private static bottomSheetContainer: HTMLElement | null = null;

  /**
   * Create floating action button
   */
  static createFloatingActionButton(config: FloatingActionButtonConfig): HTMLElement {
    const fab = document.createElement('button');
    fab.className = `floating-action-button fab-${config.position || 'bottom-right'} fab-${config.color || 'primary'}`;
    fab.innerHTML = `
      <span class="fab-icon">${config.icon}</span>
      <span class="fab-label">${config.label}</span>
    `;
    
    fab.addEventListener('click', config.onClick);
    
    // Add touch feedback
    fab.addEventListener('touchstart', () => {
      fab.style.transform = 'scale(0.95)';
    });
    
    fab.addEventListener('touchend', () => {
      fab.style.transform = 'scale(1)';
    });

    return fab;
  }

  /**
   * Show floating action button (only on mobile)
   */
  static showFloatingActionButton(config: FloatingActionButtonConfig): void {
    if (!mobileLayoutManager.isMobile()) return;

    this.hideFloatingActionButton(); // Remove any existing FAB

    if (!this.fabContainer) {
      this.fabContainer = document.createElement('div');
      this.fabContainer.className = 'fab-container';
      document.body.appendChild(this.fabContainer);
    }

    const fab = this.createFloatingActionButton(config);
    this.fabContainer.appendChild(fab);

    // Animate in
    setTimeout(() => {
      fab.classList.add('fab-visible');
    }, 10);
  }

  /**
   * Hide floating action button
   */
  static hideFloatingActionButton(): void {
    if (this.fabContainer) {
      const fabs = this.fabContainer.querySelectorAll('.floating-action-button');
      fabs.forEach(fab => {
        fab.classList.remove('fab-visible');
        setTimeout(() => {
          if (fab.parentNode) {
            fab.parentNode.removeChild(fab);
          }
        }, 300);
      });
    }
  }

  /**
   * Create bottom sheet modal
   */
  static showBottomSheet(config: BottomSheetConfig): Promise<void> {
    return new Promise((resolve) => {
      if (!mobileLayoutManager.isMobile()) {
        // On desktop, show as regular modal
        this.showDesktopModal(config);
        resolve();
        return;
      }

      this.hideBottomSheet(); // Remove any existing bottom sheet

      // Create backdrop
      const backdrop = document.createElement('div');
      backdrop.className = 'bottom-sheet-backdrop';

      // Create bottom sheet
      const bottomSheet = document.createElement('div');
      bottomSheet.className = 'bottom-sheet';
      
      const header = document.createElement('div');
      header.className = 'bottom-sheet-header';
      header.innerHTML = `
        <div class="bottom-sheet-handle"></div>
        <h3 class="bottom-sheet-title">${config.title}</h3>
        ${config.dismissible !== false ? '<button class="bottom-sheet-close">√ó</button>' : ''}
      `;

      const content = document.createElement('div');
      content.className = 'bottom-sheet-content';
      if (typeof config.content === 'string') {
        content.innerHTML = config.content;
      } else {
        content.appendChild(config.content);
      }

      const actions = document.createElement('div');
      actions.className = 'bottom-sheet-actions';
      if (config.actions) {
        config.actions.forEach(action => {
          const button = document.createElement('button');
          button.className = `btn btn-${action.style || 'secondary'}`;
          button.textContent = action.label;
          button.addEventListener('click', () => {
            action.onClick();
            this.hideBottomSheet();
            resolve();
          });
          actions.appendChild(button);
        });
      }

      bottomSheet.appendChild(header);
      bottomSheet.appendChild(content);
      if (config.actions && config.actions.length > 0) {
        bottomSheet.appendChild(actions);
      }

      // Create container
      if (!this.bottomSheetContainer) {
        this.bottomSheetContainer = document.createElement('div');
        this.bottomSheetContainer.className = 'bottom-sheet-container';
        document.body.appendChild(this.bottomSheetContainer);
      }

      this.bottomSheetContainer.appendChild(backdrop);
      this.bottomSheetContainer.appendChild(bottomSheet);

      // Add event listeners
      if (config.dismissible !== false) {
        backdrop.addEventListener('click', () => {
          this.hideBottomSheet();
          resolve();
        });

        const closeBtn = header.querySelector('.bottom-sheet-close');
        if (closeBtn) {
          closeBtn.addEventListener('click', () => {
            this.hideBottomSheet();
            resolve();
          });
        }
      }

      // Animate in
      setTimeout(() => {
        backdrop.classList.add('visible');
        bottomSheet.classList.add('visible');
      }, 10);
    });
  }

  /**
   * Hide bottom sheet
   */
  static hideBottomSheet(): void {
    if (this.bottomSheetContainer) {
      const backdrop = this.bottomSheetContainer.querySelector('.bottom-sheet-backdrop');
      const bottomSheet = this.bottomSheetContainer.querySelector('.bottom-sheet');

      if (backdrop && bottomSheet) {
        backdrop.classList.remove('visible');
        bottomSheet.classList.remove('visible');

        setTimeout(() => {
          if (this.bottomSheetContainer) {
            this.bottomSheetContainer.innerHTML = '';
          }
        }, 300);
      }
    }
  }

  /**
   * Show desktop modal (fallback for non-mobile)
   */
  private static showDesktopModal(config: BottomSheetConfig): void {
    // Simple alert fallback for desktop
    // In a real implementation, you might want to show a proper modal
    const message = typeof config.content === 'string' ? config.content : config.title;
    alert(message);
  }

  /**
   * Create mobile-optimized dropdown
   */
  static createMobileDropdown(
    trigger: HTMLElement,
    options: Array<{ label: string; value: string; onClick: () => void }>
  ): void {
    if (!mobileLayoutManager.isMobile()) return;

    trigger.addEventListener('click', () => {
      const content = document.createElement('div');
      content.className = 'mobile-dropdown-content';
      
      options.forEach(option => {
        const item = document.createElement('button');
        item.className = 'mobile-dropdown-item';
        item.textContent = option.label;
        item.addEventListener('click', () => {
          option.onClick();
          this.hideBottomSheet();
        });
        content.appendChild(item);
      });

      this.showBottomSheet({
        title: 'Select Option',
        content: content,
        dismissible: true
      });
    });
  }

  /**
   * Add loading overlay for mobile
   */
  static showMobileLoading(message: string = 'Loading...'): void {
    if (!mobileLayoutManager.isMobile()) return;

    const overlay = document.createElement('div');
    overlay.className = 'mobile-loading-overlay';
    overlay.innerHTML = `
      <div class="mobile-loading-content">
        <div class="mobile-loading-spinner"></div>
        <p class="mobile-loading-text">${message}</p>
      </div>
    `;

    document.body.appendChild(overlay);

    setTimeout(() => {
      overlay.classList.add('visible');
    }, 10);
  }

  /**
   * Hide mobile loading overlay
   */
  static hideMobileLoading(): void {
    const overlay = document.querySelector('.mobile-loading-overlay');
    if (overlay) {
      overlay.classList.remove('visible');
      setTimeout(() => {
        if (overlay.parentNode) {
          overlay.parentNode.removeChild(overlay);
        }
      }, 300);
    }
  }
}

// Auto-hide FAB when switching to desktop
mobileLayoutManager.addLayoutChangeListener((mode) => {
  if (mode !== 'mobile') {
    MobileUIPatterns.hideFloatingActionButton();
    MobileUIPatterns.hideBottomSheet();
  }
});
