/**
 * Unit Data Loader for the Army Stacking Calculator
 * Loads and manages unit data from JSON files
 */

import { 
  Unit, 
  UnitWithCalculatedProps, 
  UnitSummary, 
  UnitFilter, 
  CostType,
  UnitUtils 
} from '../types/units';

export class UnitLoader {
  private units: Unit[] = [];
  private unitsByName: Map<string, Unit> = new Map();
  private unitsByCostType: Record<CostType, Unit[]> = {
    Leadership: [],
    Dominance: [],
    Authority: [],
    Mercenary: []
  };

  constructor() {
    this.resetData();
  }

  /**
   * Load units from a JSON file or URL
   */
  async loadUnits(source: string | Unit[]): Promise<Unit[]> {
    try {
      let unitsData: Unit[];

      if (typeof source === 'string') {
        console.log(`Loading units from: ${source}`);
        const response = await fetch(source);
        
        if (!response.ok) {
          throw new Error(`Failed to fetch units: ${response.status} ${response.statusText}`);
        }
        
        unitsData = await response.json();
      } else {
        unitsData = source;
      }

      if (!Array.isArray(unitsData)) {
        throw new Error('Unit data must be an array');
      }

      this.units = unitsData.map(unitData => this.validateAndNormalizeUnit(unitData));
      this.buildLookups();

      console.log(`‚úÖ Loaded ${this.units.length} units successfully`);
      return this.units;
    } catch (error) {
      console.error('‚ùå Error loading units:', error);
      throw error;
    }
  }

  /**
   * Validate and normalize unit data
   */
  private validateAndNormalizeUnit(unitData: any): Unit {
    // Ensure all required fields exist with defaults
    const unit: Unit = {
      name: unitData.name || 'Unknown',
      unit_types: Array.isArray(unitData.unit_types) ? unitData.unit_types : [],
      cost_type: unitData.cost_type || 'Leadership',
      health: Number(unitData.health) || 0,
      strength: Number(unitData.strength) || 0,
      leadership_cost: Number(unitData.leadership_cost) || 0,
      dominance_cost: Number(unitData.dominance_cost) || 0,
      authority_cost: Number(unitData.authority_cost) || 0,
      food_consumption: Number(unitData.food_consumption) || 0,
      carrying_capacity: Number(unitData.carrying_capacity) || 0,
      revival_cost_gold: Number(unitData.revival_cost_gold) || 0,
      revival_cost_silver: Number(unitData.revival_cost_silver) || 0,
      source_file: unitData.source_file || ''
    };

    // Validate cost type
    if (!['Leadership', 'Dominance', 'Authority', 'Mercenary'].includes(unit.cost_type)) {
      console.warn(`Invalid cost type for unit ${unit.name}: ${unit.cost_type}`);
      unit.cost_type = 'Leadership'; // Default fallback
    }

    return unit;
  }

  /**
   * Build lookup dictionaries for efficient access
   */
  private buildLookups(): void {
    this.resetData();

    this.unitsByName = new Map(this.units.map(unit => [unit.name, unit]));

    // Categorize units by cost type
    this.units.forEach(unit => {
      if (UnitUtils.isMercenary(unit)) {
        this.unitsByCostType.Mercenary.push(unit);
      } else if (unit.cost_type in this.unitsByCostType) {
        this.unitsByCostType[unit.cost_type as CostType].push(unit);
      }
    });

    // Sort units within each cost type by strength
    Object.keys(this.unitsByCostType).forEach(costType => {
      this.unitsByCostType[costType as CostType].sort((a, b) => a.strength - b.strength);
    });
  }

  /**
   * Reset internal data structures
   */
  private resetData(): void {
    this.unitsByName.clear();
    this.unitsByCostType = {
      Leadership: [],
      Dominance: [],
      Authority: [],
      Mercenary: []
    };
  }

  /**
   * Get all loaded units
   */
  getAllUnits(): Unit[] {
    return [...this.units];
  }

  /**
   * Get a unit by its name
   */
  getUnitByName(name: string): Unit | undefined {
    return this.unitsByName.get(name);
  }

  /**
   * Get units by cost type
   */
  getUnitsByCostType(costType: CostType): Unit[] {
    return [...this.unitsByCostType[costType]];
  }

  /**
   * Get units that are available for army building
   */
  getAvailableUnits(availableUnitNames: string[]): Unit[] {
    const availableUnits: Unit[] = [];
    
    for (const name of availableUnitNames) {
      const unit = this.getUnitByName(name);
      if (unit) {
        availableUnits.push(unit);
      } else {
        console.warn(`Unit '${name}' not found in loaded data`);
      }
    }
    
    return availableUnits;
  }

  /**
   * Filter units by various criteria
   */
  filterUnits(filter: UnitFilter): Unit[] {
    let filtered = this.units;
    
    if (filter.costType) {
      filtered = filtered.filter(u => u.cost_type === filter.costType);
    }
    
    if (filter.unitTypes && filter.unitTypes.length > 0) {
      filtered = filtered.filter(u => 
        filter.unitTypes!.some(type => u.unit_types.includes(type))
      );
    }
    
    if (filter.minStrength !== undefined) {
      filtered = filtered.filter(u => u.strength >= filter.minStrength!);
    }
    
    if (filter.maxCost !== undefined) {
      filtered = filtered.filter(u => UnitUtils.getPrimaryCost(u) <= filter.maxCost!);
    }
    
    return filtered;
  }

  /**
   * Search units by name (case-insensitive, partial match)
   */
  searchUnits(query: string): Unit[] {
    if (!query.trim()) {
      return this.getAllUnits();
    }

    const lowerQuery = query.toLowerCase();
    return this.units.filter(unit => 
      unit.name.toLowerCase().includes(lowerQuery)
    );
  }

  /**
   * Get units with enhanced calculated properties
   */
  getEnhancedUnits(): UnitWithCalculatedProps[] {
    return this.units.map(unit => ({
      ...unit,
      get isMercenary(): boolean {
        return UnitUtils.isMercenary(unit);
      },
      get primaryCost(): number {
        return UnitUtils.getPrimaryCost(unit);
      },
      get strengthPerCost(): number {
        return UnitUtils.getStrengthPerCost(unit);
      },
      get healthPerCost(): number {
        return UnitUtils.getHealthPerCost(unit);
      }
    }));
  }

  /**
   * Get a summary of loaded units
   */
  getUnitSummary(): UnitSummary {
    if (this.units.length === 0) {
      return {
        totalUnits: 0,
        byCostType: { Leadership: 0, Dominance: 0, Authority: 0, Mercenary: 0 },
        strengthRange: { min: 0, max: 0 },
        healthRange: { min: 0, max: 0 }
      };
    }

    const strengths = this.units.map(u => u.strength);
    const healths = this.units.map(u => u.health);

    return {
      totalUnits: this.units.length,
      byCostType: {
        Leadership: this.unitsByCostType.Leadership.length,
        Dominance: this.unitsByCostType.Dominance.length,
        Authority: this.unitsByCostType.Authority.length,
        Mercenary: this.unitsByCostType.Mercenary.length
      },
      strengthRange: {
        min: Math.min(...strengths),
        max: Math.max(...strengths)
      },
      healthRange: {
        min: Math.min(...healths),
        max: Math.max(...healths)
      }
    };
  }

  /**
   * Get unique unit types from all loaded units
   */
  getUniqueUnitTypes(): string[] {
    const types = new Set<string>();
    this.units.forEach(unit => {
      unit.unit_types.forEach(type => types.add(type));
    });
    return Array.from(types).sort();
  }

  /**
   * Get statistics about the loaded units
   */
  getStatistics() {
    if (this.units.length === 0) {
      return {
        totalUnits: 0,
        costTypeDistribution: { Leadership: 0, Dominance: 0, Authority: 0, Mercenary: 0 },
        strengthStats: { min: 0, max: 0, avg: 0 },
        healthStats: { min: 0, max: 0, avg: 0 },
        topUnitsByStrength: [],
        topUnitsByHealth: []
      };
    }

    const strengths = this.units.map(u => u.strength);
    const healths = this.units.map(u => u.health);

    return {
      totalUnits: this.units.length,
      costTypeDistribution: {
        Leadership: this.unitsByCostType.Leadership.length,
        Dominance: this.unitsByCostType.Dominance.length,
        Authority: this.unitsByCostType.Authority.length,
        Mercenary: this.unitsByCostType.Mercenary.length
      },
      strengthStats: {
        min: Math.min(...strengths),
        max: Math.max(...strengths),
        avg: Math.round(strengths.reduce((a, b) => a + b, 0) / strengths.length)
      },
      healthStats: {
        min: Math.min(...healths),
        max: Math.max(...healths),
        avg: Math.round(healths.reduce((a, b) => a + b, 0) / healths.length)
      },
      topUnitsByStrength: [...this.units]
        .sort((a, b) => b.strength - a.strength)
        .slice(0, 10),
      topUnitsByHealth: [...this.units]
        .sort((a, b) => b.health - a.health)
        .slice(0, 10)
    };
  }
}
