/**
 * Mobile utility functions for responsive behavior and touch interactions
 */

export interface BreakpointConfig {
  mobileSmall: number;
  mobileLarge: number;
  tabletSmall: number;
  tabletLarge: number;
  desktop: number;
}

export const BREAKPOINTS: BreakpointConfig = {
  mobileSmall: 320,
  mobileLarge: 480,
  tabletSmall: 768,
  tabletLarge: 1024,
  desktop: 1200,
};

export type LayoutMode = 'mobile' | 'tablet' | 'desktop';

/**
 * Mobile detection and layout utilities
 */
export class MobileLayoutManager {
  private currentMode: LayoutMode = 'desktop';
  private listeners: Array<(mode: LayoutMode) => void> = [];

  constructor() {
    this.updateLayoutMode();
    this.setupResizeListener();
  }

  /**
   * Get current layout mode based on screen width
   */
  getCurrentMode(): LayoutMode {
    return this.currentMode;
  }

  /**
   * Check if current layout is mobile
   */
  isMobile(): boolean {
    return this.currentMode === 'mobile';
  }

  /**
   * Check if current layout is tablet
   */
  isTablet(): boolean {
    return this.currentMode === 'tablet';
  }

  /**
   * Check if current layout is desktop
   */
  isDesktop(): boolean {
    return this.currentMode === 'desktop';
  }

  /**
   * Check if device has touch capability
   */
  isTouchDevice(): boolean {
    return 'ontouchstart' in window || navigator.maxTouchPoints > 0;
  }

  /**
   * Get current viewport width
   */
  getViewportWidth(): number {
    return window.innerWidth;
  }

  /**
   * Add listener for layout mode changes
   */
  addLayoutChangeListener(callback: (mode: LayoutMode) => void): void {
    this.listeners.push(callback);
  }

  /**
   * Remove layout change listener
   */
  removeLayoutChangeListener(callback: (mode: LayoutMode) => void): void {
    const index = this.listeners.indexOf(callback);
    if (index > -1) {
      this.listeners.splice(index, 1);
    }
  }

  /**
   * Update layout mode based on current viewport
   */
  private updateLayoutMode(): void {
    const width = this.getViewportWidth();
    let newMode: LayoutMode;

    if (width < BREAKPOINTS.tabletSmall) {
      newMode = 'mobile';
    } else if (width < BREAKPOINTS.tabletLarge) {
      newMode = 'tablet';
    } else {
      newMode = 'desktop';
    }

    if (newMode !== this.currentMode) {
      this.currentMode = newMode;
      this.notifyListeners();
    }
  }

  /**
   * Setup resize listener for responsive behavior
   */
  private setupResizeListener(): void {
    let resizeTimeout: number;
    
    window.addEventListener('resize', () => {
      clearTimeout(resizeTimeout);
      resizeTimeout = window.setTimeout(() => {
        this.updateLayoutMode();
      }, 150);
    });
  }

  /**
   * Notify all listeners of layout change
   */
  private notifyListeners(): void {
    this.listeners.forEach(callback => callback(this.currentMode));
  }
}

/**
 * Touch interaction utilities
 */
export class TouchInteractionManager {
  /**
   * Add swipe gesture support to an element
   */
  static addSwipeSupport(
    element: HTMLElement,
    onSwipeLeft?: () => void,
    onSwipeRight?: () => void,
    threshold: number = 50
  ): void {
    if (!('ontouchstart' in window)) return;

    let startX: number = 0;
    let startY: number = 0;
    let startTime: number = 0;

    element.addEventListener('touchstart', (e) => {
      const touch = e.touches[0];
      startX = touch.clientX;
      startY = touch.clientY;
      startTime = Date.now();
    }, { passive: true });

    element.addEventListener('touchend', (e) => {
      if (e.changedTouches.length === 0) return;

      const touch = e.changedTouches[0];
      const endX = touch.clientX;
      const endY = touch.clientY;
      const endTime = Date.now();

      const deltaX = endX - startX;
      const deltaY = endY - startY;
      const deltaTime = endTime - startTime;

      // Check if it's a valid swipe (not too slow, not too vertical)
      if (deltaTime < 500 && Math.abs(deltaX) > threshold && Math.abs(deltaY) < Math.abs(deltaX) * 0.5) {
        if (deltaX > 0 && onSwipeRight) {
          e.preventDefault();
          onSwipeRight();
        } else if (deltaX < 0 && onSwipeLeft) {
          e.preventDefault();
          onSwipeLeft();
        }
      }
    }, { passive: false });
  }
  /**
   * Add touch-friendly event listeners to an element
   */
  static addTouchSupport(element: HTMLElement, callback: () => void): void {
    // Add both click and touch events for better compatibility
    element.addEventListener('click', callback);
    
    if ('ontouchstart' in window) {
      let touchStartTime: number;
      
      element.addEventListener('touchstart', (e) => {
        touchStartTime = Date.now();
        element.classList.add('touch-active');
      });
      
      element.addEventListener('touchend', (e) => {
        element.classList.remove('touch-active');
        
        // Only trigger if it was a quick tap (not a scroll)
        if (Date.now() - touchStartTime < 200) {
          e.preventDefault();
          callback();
        }
      });
      
      element.addEventListener('touchcancel', () => {
        element.classList.remove('touch-active');
      });
    }
  }

  /**
   * Optimize scrollable element for mobile
   */
  static optimizeScrolling(element: HTMLElement): void {
    // Add momentum scrolling for iOS
    element.style.webkitOverflowScrolling = 'touch';
    element.style.scrollBehavior = 'smooth';
    
    // Add scroll indicators
    element.classList.add('scroll-momentum');
  }

  /**
   * Add haptic feedback simulation (visual feedback)
   */
  static addHapticFeedback(element: HTMLElement): void {
    element.addEventListener('touchstart', () => {
      element.style.transform = 'scale(0.98)';
      element.style.transition = 'transform 0.1s ease';
    });

    element.addEventListener('touchend', () => {
      element.style.transform = 'scale(1)';
    });

    element.addEventListener('touchcancel', () => {
      element.style.transform = 'scale(1)';
    });
  }

  /**
   * Add pull-to-refresh functionality
   */
  static addPullToRefresh(
    container: HTMLElement,
    onRefresh: () => Promise<void>,
    threshold: number = 80
  ): void {
    if (!('ontouchstart' in window)) return;

    let startY: number = 0;
    let currentY: number = 0;
    let isRefreshing: boolean = false;
    let refreshIndicator: HTMLElement | null = null;

    // Create refresh indicator
    const createRefreshIndicator = () => {
      if (refreshIndicator) return;

      refreshIndicator = document.createElement('div');
      refreshIndicator.className = 'pull-refresh-indicator';
      refreshIndicator.innerHTML = `
        <div class="refresh-spinner"></div>
        <span class="refresh-text">Pull to refresh</span>
      `;
      container.insertBefore(refreshIndicator, container.firstChild);
    };

    container.addEventListener('touchstart', (e) => {
      if (container.scrollTop === 0 && !isRefreshing) {
        startY = e.touches[0].clientY;
        createRefreshIndicator();
      }
    }, { passive: true });

    container.addEventListener('touchmove', (e) => {
      if (container.scrollTop === 0 && !isRefreshing && refreshIndicator) {
        currentY = e.touches[0].clientY;
        const pullDistance = Math.max(0, currentY - startY);

        if (pullDistance > 0) {
          e.preventDefault();
          const progress = Math.min(pullDistance / threshold, 1);
          refreshIndicator.style.transform = `translateY(${pullDistance * 0.5}px)`;
          refreshIndicator.style.opacity = progress.toString();

          if (pullDistance > threshold) {
            refreshIndicator.querySelector('.refresh-text')!.textContent = 'Release to refresh';
          } else {
            refreshIndicator.querySelector('.refresh-text')!.textContent = 'Pull to refresh';
          }
        }
      }
    }, { passive: false });

    container.addEventListener('touchend', async () => {
      if (refreshIndicator && !isRefreshing) {
        const pullDistance = currentY - startY;

        if (pullDistance > threshold) {
          isRefreshing = true;
          refreshIndicator.querySelector('.refresh-text')!.textContent = 'Refreshing...';
          refreshIndicator.querySelector('.refresh-spinner')!.classList.add('spinning');

          try {
            await onRefresh();
          } finally {
            isRefreshing = false;
            if (refreshIndicator) {
              refreshIndicator.style.transform = 'translateY(-100%)';
              refreshIndicator.style.opacity = '0';
              setTimeout(() => {
                if (refreshIndicator && refreshIndicator.parentNode) {
                  refreshIndicator.parentNode.removeChild(refreshIndicator);
                  refreshIndicator = null;
                }
              }, 300);
            }
          }
        } else {
          refreshIndicator.style.transform = 'translateY(-100%)';
          refreshIndicator.style.opacity = '0';
          setTimeout(() => {
            if (refreshIndicator && refreshIndicator.parentNode) {
              refreshIndicator.parentNode.removeChild(refreshIndicator);
              refreshIndicator = null;
            }
          }, 300);
        }
      }
    });
  }
}

/**
 * Mobile-specific CSS utilities
 */
export class MobileCSSManager {
  /**
   * Add mobile-specific CSS classes to body based on current layout
   */
  static updateBodyClasses(layoutManager: MobileLayoutManager): void {
    const body = document.body;
    
    // Remove existing layout classes
    body.classList.remove('layout-mobile', 'layout-tablet', 'layout-desktop');
    
    // Add current layout class
    body.classList.add(`layout-${layoutManager.getCurrentMode()}`);
    
    // Add touch device class if applicable
    if (layoutManager.isTouchDevice()) {
      body.classList.add('touch-device');
    }
  }

  /**
   * Apply mobile-optimized styles to combat logs
   */
  static optimizeCombatLogs(): void {
    const combatLogs = document.querySelectorAll('.combat-log');
    
    combatLogs.forEach(log => {
      if (log instanceof HTMLElement) {
        TouchInteractionManager.optimizeScrolling(log);
      }
    });
  }

  /**
   * Apply mobile-optimized styles to unit cards
   */
  static optimizeUnitCards(): void {
    const unitCards = document.querySelectorAll('.unit-card');
    
    unitCards.forEach(card => {
      if (card instanceof HTMLElement) {
        TouchInteractionManager.addHapticFeedback(card);
      }
    });
  }
}

// Global mobile layout manager instance
export const mobileLayoutManager = new MobileLayoutManager();

// Auto-update body classes when layout changes
mobileLayoutManager.addLayoutChangeListener(() => {
  MobileCSSManager.updateBodyClasses(mobileLayoutManager);
});

// Initialize body classes
MobileCSSManager.updateBodyClasses(mobileLayoutManager);
